@use 'helpers/mixins' as *;

.promo {
  padding-bottom: 170px;
  position: relative;

  &__illustration {
    position: absolute;
    top: 105px;
    left: 50%;
    transform: translateX(-50%);
  }

  &__wrapper {
    display: flex;
    justify-content: unset;
    align-items: unset;
  }

  &__text {
    color: var(--brown);
    text-transform: uppercase;
    margin-right: 98px;
    width: 199px;
  }

  &__title {
    position: relative;

    h2 {
      font-weight: 400;

      span {
        &:nth-child(3) {
          padding-left: 215px;
        }
        &:nth-child(5) {
          padding-left: 375px;
        }
      }
    }

    .promo__picture-one {
      border-radius: 214px;
      position: absolute;
      top: 105px;
      left: 0;
      max-width: 156px;
    }

    .promo__picture-two {
      border-radius: 102px;
      position: absolute;
      right: 120px;
      bottom: 0;
      max-width: 105px;
    }
  }
}

@include media(max, 1399) {
  .promo {
    &__illustration {
      top: 75px;
    }

    &__text {
      margin-right: 20px;
    }

    &__title {
      position: relative;

      h2 {
        font-size: 50px;
        line-height: 56px;

        span {
          &:nth-child(3) {
            padding-left: 185px;
          }
          &:nth-child(5) {
            padding-left: 255px;
          }
        }
      }

      .promo__picture-one {
        left: 0;
        top: 58px;
      }

      .promo__picture-two {
        right: 30px;
        bottom: 0;
      }
    }
  }
}

@include media(max, 991) {
  .promo {
    &__wrapper {
      justify-content: space-between;
    }

    &__illustration {
      top: 65px;
    }

    &__text {
      margin-right: 0;
      font-size: 14px;
      width: 175px;
    }

    &__title {
      position: relative;

      h2 {
        font-size: 38px;
        line-height: 40px;

        span {
          &:nth-child(3) {
            padding-left: 130px;
          }
          &:nth-child(5) {
            padding-left: 190px;
          }
        }
      }

      .promo__picture-one {
        top: 42px;
        width: 110px;
      }

      .promo__picture-two {
        right: 30px;
        width: 80px;
      }
    }
  }
}

@include media(max, 767) {
  .promo {
    padding-bottom: 98px;

    &__wrapper {
      flex-direction: column;
    }

    &__illustration {
      top: 170px;
    }

    &__text {
      margin-right: 0;
      font-size: 14px;
      width: 380px;
      font-size: 16px;
      line-height: 18px;
      margin-bottom: 45px;
    }

    &__title {
      h2 {
        letter-spacing: -0.48px;

        span {
          &:nth-child(1) {
            padding-left: 150px;
          }
          &:nth-child(3) {
            padding-left: 125px;
          }
          &:nth-child(5) {
            padding-left: 135px;
          }
        }
      }

      .promo__picture-one {
        top: 15px;
        width: 115px;
      }

      .promo__picture-two {
        left: 390px;
        bottom: 0;
        width: 80px;
      }
    }
  }
}

@include media(max, 575) {
  .promo {
    padding-bottom: 98px;

    &__wrapper {
      flex-direction: column;
      align-items: center;
    }

    &__illustration {
      top: 125px;
    }

    &__text {
      margin-right: 0;
      font-size: 14px;
      width: 332px;
      font-size: 12px;
      line-height: 18px;
      margin-bottom: 45px;
    }

    &__title {
      h2 {
        font-size: 24px;
        line-height: 26px;
        letter-spacing: -0.48px;

        span {
          &:nth-child(1) {
            padding-left: 121px;
          }
          &:nth-child(3) {
            padding-left: 104px;
          }
          &:nth-child(5) {
            padding-left: 112px;
          }
        }
      }

      .promo__picture-one {
        top: 5px;
        width: 93px;
      }

      .promo__picture-two {
        left: 256px;
        bottom: -20px;
        width: 62px;
      }
    }
  }
}

@include media(max, 374) {
  .promo__title h2 {
    font-size: 22px;
  }
}
