@use 'helpers/mixins' as *;

.faq-body {
  .page-main {
    overflow: unset;
  }
}

.faq {
  padding-top: 70px;

  &__title {
    max-width: 540px;
    width: 100%;
    text-align: center;
    margin: 0 auto;
    margin-bottom: 120px;

    h1 {
      margin-bottom: 16px;
      font-weight: 400;
    }

    p {
      color: var(--brown);
    }
  }

  &__wrapper {
    display: grid;
    align-items: flex-start;
    grid-template-columns: 426px 1fr;
    gap: 68px;
  }

  .faq-left {
    position: sticky;
    top: 30px;
  }

  .faq-right {
    h4 {
      color: #645140;
      font-family: var(--main-font);
      line-height: 32px;
      font-weight: 600;
      margin-bottom: 46px;
    }
  }

  .accordion {
    &:not(:last-child) {
      margin-bottom: 80px;
    }
  }
}

@include media(max, 1199) {
  .faq {
    &__wrapper {
      grid-template-columns: 400px 1fr;
      gap: 58px;
    }
  }
}

@include media(max, 991) {
  .faq {
    &__wrapper {
      grid-template-columns: 300px 1fr;
      gap: 38px;
    }
  }
}

@include media(max, 767) {
  .faq {
    &__title {
      margin-bottom: 100px;

      p {
        max-width: 450px;
        margin: 0 auto;
      }
    }

    &__wrapper {
      grid-template-columns: 1fr;
      gap: 38px;
    }

    .faq-left {
      display: none;
    }
  }
}

@include media(max, 575) {
  .faq {
    padding-top: 68px;

    &__title {
      margin-bottom: 68px;

      h1 {
        font-size: 36px;
        font-weight: 400;
        line-height: 34px;
        letter-spacing: -0.72px;
        text-transform: uppercase;
        margin-bottom: 23px;
      }

      p {
        color: var(--brown);
        font-size: 12px;
        font-weight: 400;
        line-height: 18px;
        max-width: 275px;
        margin: 0 auto;
      }
    }

    &__wrapper {
      display: grid;
      align-items: flex-start;
      grid-template-columns: 1fr;
      gap: 68px;
    }

    .faq-left {
      display: none;
    }

    .faq-right {
      h4 {
        font-size: 24px;
        font-weight: 500;
        line-height: 28px;
        margin-bottom: 32px;
      }
    }

    .accordion {
      &:not(:last-child) {
        margin-bottom: 80px;
      }
    }
  }
}
