@use 'helpers/mixins' as *;

.catalog-hero {
  background: #f5f2ee;

  &__wrapper {
    display: flex;
    gap: 73px;
    max-width: 1440px;
    width: 100%;
    margin: 0 auto;
    padding: 0 15px;
  }

  .hero-image {
    max-width: 696px;
    width: 100%;
    object-fit: cover;
  }

  .hero__title {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 68px 0 71px;

    h1 {
      font-weight: 400;
      max-width: 586px;
      width: 100%;
    }

    .description-text {
      display: flex;
      flex-direction: column;
      gap: 24px;
      max-width: 500px;
      width: 100%;
      color: #645140;
    }
  }
}

@include media(max, 1399) {
  .catalog-hero {
    &__wrapper {
      gap: 63px;
    }

    .hero-image {
      max-width: 526px;
    }
  }
}

@include media(max, 1199) {
  .catalog-hero {
    &__wrapper {
      gap: 45px;
    }

    .hero-image {
      max-width: 405px;
    }
  }
}

@include media(max, 991) {
  .catalog-hero {
    &__wrapper {
      gap: 20px;
    }

    .hero-image {
      max-width: 355px;
    }

    .hero__title {
      h1 {
        margin-bottom: 20px;
        font-size: 48px;
        line-height: 56px;
      }
    }
  }
}

@include media(max, 767) {
  .catalog-hero {
    &__wrapper {
      flex-direction: column-reverse;
      gap: 20px;
    }

    .hero-image {
      max-width: 100%;
    }

    .hero__title {
      padding: 40px 0 12px;
      h1 {
        margin-bottom: 24px;
        font-size: 48px;
        line-height: 56px;
      }
    }
  }
}

@include media(max, 575) {
  .catalog-hero {
    &__wrapper {
      gap: 0;
      max-width: 500px;
    }

    .hero-image {
      max-width: 100%;
      margin-bottom: -40px;
    }

    .hero__title {
      padding: 48px 0 48px;
      h1 {
        margin-bottom: 22px;
        font-size: 36px;
        line-height: 34px;
        letter-spacing: -0.72px;
      }
      .description-text {
        font-size: 12px;
        line-height: 18px;
        gap: 20px;
      }
    }
  }
}
