@use 'helpers/mixins' as *;

.blog {
  padding: 70px 0 100px;

  .main-text {
    margin-bottom: 50px;
    font-weight: 400;
  }

  &__wrapper {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 50px 26px;
    margin-bottom: 50px;
  }

  &__article {
    display: flex;
    flex-direction: column;
    position: relative;
    padding: 26px 30px 40px;
    border-radius: 20px;
    border: 1px solid transparent;
    transition: var(--transition);

    &:hover {
      border-color: var(--brown);

      .blog-arrow {
        opacity: 1;
        visibility: visible;
      }
    }

    span {
      color: var(--gray);
      margin-bottom: 20px;

      + img {
        margin-bottom: 30px;
        max-height: 168px;
        object-fit: cover;
        object-position: center;
      }
    }

    h4 {
      color: var(--brown);
      font-family: var(--second-font);
      font-size: 24px;
      font-weight: 400;
      line-height: 32px;
      letter-spacing: -0.48px;
      text-transform: uppercase;
      margin-bottom: 9px;
    }

    p {
      color: var(--brown);
      display: -webkit-box;
      -webkit-line-clamp: 4;
      -webkit-box-orient: vertical;
      overflow: hidden;
    }

    .blog-arrow {
      position: absolute;
      top: 26px;
      right: 30px;
      opacity: 0;
      visibility: hidden;
      transition: var(--transition);
    }
  }

  .more-articles {
    display: flex;
    justify-content: center;
    width: 100%;

    button {
      display: flex;
      justify-content: center;
      font-family: var(--second-font);
      align-items: center;
      padding: 0 35px;
      border: 1px solid var(--light-gray);
      border-radius: 26px;
      line-height: 50px;
      color: var(--brown);
      height: 52px;
      transition: var(--transition);
      text-decoration: underline;

      &:hover {
        border-color: var(--brown);
        text-decoration: underline;
      }
    }
  }
}

@include media(max, 1399) {
  .blog {
    &__wrapper {
      grid-template-columns: repeat(2, 1fr);
    }
  }
}

@include media(max, 767) {
  .blog {
    &__wrapper {
      grid-template-columns: 100%;
    }
  }
}

@include media(max, 575) {
  .blog {
    padding: 48px 0 68px;

    .main-text {
      margin-bottom: 48px;
    }

    &__wrapper {
      gap: 10px;
      margin-bottom: 50px;
    }

    &__article {
      padding: 20px;
      span {
        font-size: 12px;
        line-height: 24px;
        + img {
          margin-bottom: 30px;
          min-height: 168px;
        }
      }

      h4 {
        line-height: 26px;
        letter-spacing: -0.48px;
        margin-bottom: 15px;
      }

      p {
        font-size: 12px;
        line-height: 18px;
      }

      .blog-arrow {
        top: 16px;
        right: 16px;
      }
    }

    .more-articles {
      button {
        height: 43px;
        padding: 0 24px;
        font-size: 12px;
        font-weight: 400;
        line-height: 56px;
      }
    }
  }
}
