@use 'helpers/mixins' as *;

.feedback {
  padding-bottom: 180px;
  overflow: hidden;
  position: relative;

  .bg-text {
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 100%;
    position: absolute;
    top: 236px;
    color: #dfd8cf;
    font-family: var(--second-font);
    font-size: 48px;
    font-weight: 400;
    line-height: 56px;
    letter-spacing: -0.96px;
    text-transform: uppercase;
    opacity: 0.3;
    left: 50%;
    transform: translateX(-50%);

    p {
      white-space: nowrap;

      &::before {
        content: '';
        display: block;
        width: 100%;
        background: var(--light-gray);
        height: 1px;
        margin-bottom: 30px;
      }

      &:first-child {
        margin-left: -20px;
      }

      &:last-child {
        border-bottom: 1px solid var(--light-gray);
        padding: 30px 0 30px 30px;
      }
    }
  }

  .main-text {
    text-align: center;
    font-weight: 400;
    margin-bottom: 80px;
  }

  &__wrapper {
    display: flex;
    align-items: center;
    position: relative;
    max-width: 1046px;
    margin: 0 auto;
    margin-bottom: 56px;
  }

  &__carousel {
    max-width: 790px;
    width: 100%;
    overflow: hidden;
    margin: 0 auto;
  }

  .swiper {
    position: relative;
    max-width: 1046px;
    width: 100%;
    max-width: 222px;
    overflow: unset;

    .swiper-button-next {
      right: 0;
    }

    .swiper-button-prev {
      left: 0;
    }
  }

  .swiper-slide {
    width: 222px;
    height: fit-content;
    transform: scale(0.8);
    opacity: 1;
    transition: var(--transition);

    video {
      width: 100%;
      height: 100%;
    }
  }

  .swiper-slide-active {
    transform: scale(1);

    > div {
      &::before {
        content: unset;
      }
    }

    video {
      transform: scale(1);
    }
  }

  .swiper-button-next,
  .swiper-button-prev {
    width: 58px;
    height: 58px;
    border: 1px solid var(--light-gray);
    border-radius: 50%;
    margin: 0;
    background: #faf9f8;
    --swiper-navigation-sides-offset: 0;

    transform: translateY(-50%);

    > div {
      position: static;
      margin: 0;
      transition: var(--transition);
      position: absolute;
      width: 100%;
      top: 50%;
      transform: translateY(-50%);
      z-index: 5;
    }

    svg {
      width: 22px;
      height: 15px;
    }

    &::after {
      content: unset;
    }
  }

  &__video {
    position: relative;
    cursor: pointer;
    &::before {
      content: '';
      position: absolute;
      z-index: 1;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
      background: rgba(217, 217, 217, 0.01);
      backdrop-filter: blur(5px);
    }
  }

  &__user {
    display: flex;
    align-items: center;
    gap: 8px;
    position: absolute;
    top: 12px;
    left: 12px;
    z-index: 1;

    p {
      font-size: 10px;
      line-height: 12px;
      color: #fff;
    }
  }

  .controls {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 4;
  }

  .play-pause {
    transition: var(--transition);
    &.is-active {
      transform: scale(0);
    }
    &::before {
      content: '';
      display: block;
      width: 48px;
      height: 48px;
      opacity: 0.2;
      background: #fff;
      border-radius: 48px;
    }

    > svg {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      transition: var(--transition);

      &:hover {
        opacity: 0.4;
      }
    }
  }

  &__text {
    max-width: 656px;
    margin: 0 auto;
    text-align: center;
    color: var(--brown);

    p {
      &:first-child {
        margin-bottom: 20px;
      }
    }
  }

  &__socials {
    display: flex;
    justify-content: center;
    gap: 12px;

    a:hover svg rect,
    a:hover svg circle,
    a:hover svg path {
      opacity: 0.4;
      transition: var(--transition);
    }
  }
}

@include media(max, 991) {
  .feedback {
    .swiper-button-next,
    .swiper-button-prev {
      display: none;
    }
  }
}

@include media(max, 767) {
  .feedback {
    .swiper-button-next,
    .swiper-button-prev {
      display: none;
    }

    &__carousel {
      overflow: visible;
    }

    .swiper {
      max-width: 194px;
    }
  }
}

@include media(max, 575) {
  .feedback {
    padding-bottom: 100px;

    &__wrapper {
      margin-bottom: 36px;
    }

    .bg-text {
      top: 110px;
      p {
        &::before {
          margin-bottom: 18px;
        }

        &:last-child {
          margin-top: 18px;
          padding-bottom: 18px;
        }
      }
    }

    .main-text {
      margin-bottom: 42px;
    }

    .swiper-slide {
      transform: scale(0.8);
      height: fit-content;

      video {
        width: 100%;
        height: 100%;
      }
    }

    .swiper-slide-active {
      transform: scale(1);

      video {
        transform: scale(1);
      }
    }

    br {
      display: none;
    }

    &__text {
      font-size: 12px;
      line-height: 18px;
      max-width: 338px;
    }
  }
}
