@use 'helpers/mixins' as *;

$mouse-devise: '(hover: hover) and (pointer: fine)';
$touch-devise: '(hover: none) and (pointer: coarse)';

$desktop: 'screen and (max-width: 1439px)';
$laptop: 'screen and (max-width: 1023px)';
$mobile: 'screen and (max-width: 575px)';

@media #{$desktop} {
}

:root {
  --main-font: 'Montserrat', sans-serif;
  --second-font: 'Tenor Sans', sans-serif;
  --font-size: 16px;
  --line-height: 24px;
  --transition: 0.25s ease;

  --brown: #645140;
  --dark-gray: #80786f;
  --gray: #b1aca6;
  --light-gray: #d6d1ca;
  --beige: #d7bca3;
  --peach: #efe0d0;
  --white: #f5f3f2;
}

.fancybox__container {
  --fancybox-bg: rgb(0 0 0 / 60%);
}
