@use 'helpers/mixins' as *;

.article {
  padding: 63px 0 100px;
  position: relative;

  .decor-block {
    background: #f5f2ee;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    height: 612px;
    width: 100%;
  }

  &__wrapper {
    max-width: 878px;
    width: 100%;
    margin: 0 auto;
  }

  .block-navigation {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 37px;
  }

  &-date {
    color: var(--gray);
  }

  &__title {
    color: #645140;
    font-size: 48px;
    font-weight: 400;
    line-height: 54px;
    letter-spacing: -0.96px;
    text-transform: uppercase;
    margin-bottom: 52px;
  }

  img {
    margin-bottom: 82px;
  }

  h4 {
    color: var(--brown);
    font-family: var(--main-font);
    font-size: 32px;
    font-weight: 500;
    line-height: 38px;
    margin-bottom: 32px;
    text-transform: none;
  }

  p {
    color: var(--brown);
    margin-bottom: 24px;

    + p {
      margin-bottom: 24px;
    }

    + h4 {
      margin-top: 60px;
    }
  }

  .properties {
    display: flex;
    align-items: center;
    position: relative;
    margin: 60px 0;
    padding-left: 25px;
    font-size: 24px;
    line-height: 28px;
    color: var(--brown);

    &::before {
      content: '';
      border-left: 1px solid var(--brown);
      display: block;
      height: 100px;
      position: absolute;
      left: 0;
    }
  }

  li {
    list-style-type: disc;
    color: var(--brown);
    font-weight: 500;
    margin-bottom: 13px;

    + p {
      margin-left: -20px;
    }
  }
}

@include media(max, 575) {
  .article {
    padding: 48px 0 67px;

    .decor-block {
      height: 520px;
    }

    .block-navigation {
      margin-bottom: 48px;

      svg {
        width: 32px;
        height: 22px;
      }
    }

    &-date {
      color: var(--gray);
      font-size: 12px;
      line-height: 24px;
    }

    &__title {
      font-size: 36px;
      line-height: 34px;
      letter-spacing: -0.72px;
      margin-bottom: 37px;
    }

    img {
      margin-bottom: 68px;
      min-height: 284px;
      object-fit: cover;
    }

    h4 {
      font-size: 24px;
      font-weight: 500;
      line-height: 28px;
      margin-bottom: 28px;
    }

    p {
      font-size: 12px;
      line-height: 18px;
      margin-bottom: 22px;

      + p {
        margin-bottom: 22px;
      }

      + h4 {
        margin-top: 40px;
      }
    }

    .properties {
      font-size: 16px;
      font-weight: 400;
      line-height: 20px;
      padding-left: 18px;

      &::before {
        height: 106px;
      }
    }

    ul {
      padding-left: 20px;
    }

    li {
      margin-left: -5px;
      font-size: 12px;
      font-weight: 500;
      line-height: 18px;
      margin-bottom: 15px;
    }
  }
}
