* {
  box-sizing: border-box;
  &:before {
    box-sizing: border-box;
  }
  &:after {
    box-sizing: border-box;
  }
}

.page {
  scroll-behavior: smooth;
}

.page__body {
  font-family: var(--main-font);
  font-size: var(--font-size);
  line-height: var(--line-height);
  height: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: #faf9f8;

  &.active {
    overflow: hidden;
  }
}

.page-main {
  overflow: hidden;
}

.page-footer {
  margin-top: auto;
}

img {
  max-width: 100%;
  height: auto;
}

.img-fluid {
  max-width: 100%;
  height: auto;
}

img,
svg,
iframe {
  vertical-align: top;
}

::placeholder {
  opacity: 1;
}

::selection {
  background-color: #000;
  color: #fff;
}

h1,
h2,
h3,
h4 {
  font-family: var(--second-font);
  color: #645140;
  text-transform: uppercase;
  margin: 0;
}

p {
  margin: 0;
}

li {
  list-style-type: none;
}
