@use 'helpers/mixins' as *;

.cosmetics {
  padding-bottom: 180px;
  overflow: hidden;

  .cosmeticsSwiper {
    overflow: visible;
    position: relative;
  }

  .cosmetics__wrapper {
    position: relative;
    overflow: hidden;

    &::after {
      content: '';
      pointer-events: none;
      position: absolute;
      right: 0;
      bottom: 0;
      top: -18px;
      width: 288px;
      height: 305px;
      transform: rotate(-90deg);
      z-index: 12;
      border-radius: 20px 20px 0px 0px;
      background: linear-gradient(180deg, rgba(250, 249, 248, 0) 0%, #faf9f8 100%);
    }
  }

  .swiper-button-next,
  .swiper-button-prev {
    content: '';
  }

  .swiper-slide {
    width: 512px;
  }

  .arrows__wrapper {
    display: flex;
    justify-content: flex-end;
  }

  &__arrows {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 126px;
    margin-top: 44px;
    > div {
      position: static;
      margin: 0;
      width: 51px;
      height: 20px;
      transition: var(--transition);
      &::after {
        content: unset;
      }
    }
  }

  &__title {
    display: flex;
    max-width: 1163px;
    margin-bottom: 50px;

    h3 {
      margin-right: 38px;
      letter-spacing: -0.96px;
    }

    p {
      max-width: 279px;
      width: 100%;
      color: var(--dark-gray);
    }
  }

  &__info {
    display: flex;

    &-item {
      padding: 28px 36px 44px 32px;
      border-radius: 20px;
      min-height: 268px;
      border: 1px solid var(--light-gray);

      &:not(:last-child) {
        margin-right: 28px;
      }

      p {
        color: var(--dark-gray);
      }
    }
  }

  &__details {
    display: flex;
    align-items: center;
    gap: 20px;
    margin-bottom: 16px;
  }
}

@include media(max, 991) {
  .cosmetics__title {
    margin-bottom: 28px;
    flex-direction: column;

    h3 {
      margin-right: 0;
      width: 450px;
      margin-bottom: 18px;
    }
    p {
      max-width: 100%;
    }
  }
}

@include media(max, 767) {
  .cosmetics {
    .cosmetics__wrapper {
      &::after {
        content: unset;
      }
    }
  }
}

@include media(max, 575) {
  .cosmetics {
    padding-bottom: 94px;

    .swiper-slide {
      max-width: 343px;
      width: 100%;
    }

    &__title {
      flex-direction: column;
      margin-bottom: 27px;

      h3 {
        margin-right: 0;
        margin-bottom: 24px;
        letter-spacing: -0.48px;
        max-width: 338px;
        width: 100%;
      }

      p {
        font-size: 12px;
        line-height: 18px;
        max-width: 100%;
      }
    }

    &__info {
      &-item {
        padding: 20px 33px 30px 20px;
        min-height: 228px;

        p {
          font-size: 12px;
          line-height: 18px;
        }
      }
    }

    &__details {
      gap: 8px;
      margin-bottom: 8px;
      img {
        width: 34px;
        height: 34px;
      }
    }

    &__arrows {
      width: 87px;
      margin-top: 27px;

      svg {
        width: 32px;
      }
    }
  }
}
