.animate {
  animation-duration: 1s;
  animation-fill-mode: both;
}

.animate-from-left {
  opacity: 0;
  transform: translateX(-100px);
}

.animate-from-right {
  opacity: 0;
  transform: translateX(100px);
}

.animate-from-top {
  opacity: 0;
  transform: translateY(50px);
}

.scale-img {
  overflow: hidden;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
