@use 'helpers/mixins' as *;

.advantage {
  padding-bottom: 180px;
  position: relative;
  overflow: hidden;

  &::before,
  &::after {
    content: '';
    position: absolute;
    width: 100%;
  }

  &::before {
    top: 0;
    border-top: 1px solid var(--light-gray);
  }

  &::after {
    top: 104px;
    border-bottom: 1px solid var(--light-gray);
  }

  .line {
    border-bottom: 1px solid var(--light-gray);
    position: absolute;
    top: 260px;
    width: 100%;
  }

  &__headline {
    display: flex;
    align-items: center;
    justify-content: space-between;
    overflow: visible;
    margin-bottom: 26px;
    padding: 26px 30px 0 80px;

    img {
      height: 41px;
      width: 41px;
    }
  }

  &__title {
    display: flex;
    position: relative;
    padding-left: 80px;
    padding-top: 27px;

    .wrapper-scale-img {
      margin-right: 70px;
      flex-shrink: 0;
      max-width: 318px;
      width: 100%;
    }
  }

  &__text {
    h3 {
      letter-spacing: -0.96px;
      margin-bottom: 88px;
    }
    .light {
      color: var(--beige);
    }
  }

  &__subtext {
    display: flex;
    gap: 124px;
    color: var(--dark-gray);

    span {
      font-weight: 500;
    }

    .bold-text {
      color: var(--brown);
      text-transform: uppercase;
      font-weight: 500;
      display: block;
      margin-top: 38px;
    }

    p {
      &:first-child {
        max-width: 313px;
      }

      &:nth-child(2),
      &:nth-child(3) {
        max-width: 289px;
      }
    }
  }
}

@include media(max, 1399) {
  .advantage {
    .line {
      top: 310px;
    }

    &__headline {
      padding-left: 30px;
    }

    &__title {
      padding-left: 30px;
      &::after {
        top: 175px;
      }

      .wrapper-scale-img {
        margin-right: 40px;
      }
    }

    &__text {
      h3 {
        margin-bottom: 45px;
        max-width: 732px;
      }
    }

    &__subtext {
      .bold-text {
        margin-top: 18px;
      }
    }
  }
}

@include media(max, 1199) {
  .advantage {
    &__headline {
      padding: 26px 0 0 0;
      h3 {
        padding-left: 0;
      }
      img {
        padding-right: 0;
      }
    }

    &__title {
      padding-left: 0;
      .wrapper-scale-img {
        margin-right: 30px;
      }
    }

    &__text {
      br {
        display: none;
      }
    }

    &__subtext {
      gap: 50px;
    }
  }
}

@include media(max, 991) {
  .advantage {
    &::after {
      top: 95px;
    }

    &__title {
      .wrapper-scale-img {
        width: 300px;
        height: 100%;
      }
    }

    &__headline {
      padding: 26px 0 0 0;
    }

    &__subtext {
      gap: 50px;
    }

    .advantage__subtext {
      flex-direction: column;
      gap: 18px;
    }
  }
}

@include media(max, 767) {
  .advantage {
    &::after {
      top: 74px;
    }

    .line {
      top: 290px;
    }

    &__headline {
      padding: 20px 0;
      margin-bottom: 20px;

      h3 {
        font-size: 26px;
        line-height: 34px;
      }

      img {
        width: 33px;
        height: 33px;
      }
    }
    &__title {
      padding-top: 0;
      .wrapper-scale-img {
        width: 210px;
        height: 251px;
        margin-top: 147px;
        object-fit: cover;
      }
    }

    &__text {
      h3 {
        margin-bottom: 49px;
        font-size: 26px;
        line-height: 34px;
      }
    }

    &__subtext {
      font-size: 14px;
      line-height: 20px;
    }

    .advantage__subtext {
      p {
        &:first-child {
          position: absolute;
          top: 0;
          left: 0;
          width: 205px;
        }
        &:last-child {
          position: absolute;
          bottom: 30px;
        }
      }
    }
  }
}

@include media(max, 575) {
  .advantage {
    padding-bottom: 90px;

    &::after {
      top: 92px;
    }

    .line {
      top: 281px;
    }

    &__headline {
      h3 {
        max-width: 260px;
        line-height: 26px;
      }

      img {
        height: 33px;
        width: 33px;
      }
    }

    &__title {
      .wrapper-scale-img {
        width: 160px;
        height: 231px;
        margin-top: 130px;
      }
    }

    &__text {
      br {
        display: block;
      }

      h3 {
        margin-bottom: 0;
        font-size: 24px;
        line-height: 26px;
        letter-spacing: -0.48px;
      }
    }

    &__subtext {
      font-size: 12px;
      line-height: 18px;
    }

    .advantage__subtext {
      p {
        &:first-child {
          width: 155px;
        }
        &:last-child {
          bottom: 0;
          margin-bottom: 20px;
          @include media(max, 374) {
            margin-bottom: 0;
          }
        }
      }

      span {
        font-weight: 400;
      }

      .bold-text {
        font-weight: 500;
      }
    }
  }
}
