@use '../../helpers/mixins' as *;

.hero {
  background: no-repeat center/cover url('../img/hero-bg.jpg');
  margin-top: 0;
  position: relative;
  z-index: 1;
  overflow: hidden;
  padding: 352px 0 227px 0;

  &__wrapper {
    position: relative;
  }

  &__text {
    min-height: 66px;
  }

  &__illustration {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: -50px;
    pointer-events: none;
    img {
      max-width: unset;
      margin-bottom: 60px;
    }
  }

  &__info {
    position: relative;

    p {
      color: var(--peach);
      text-transform: uppercase;
      margin-bottom: 0;
    }
  }

  &__logo {
    margin-bottom: 16px;
  }
}

@include media(max, 575) {
  .hero {
    padding: 420px 0 185px 0;
    background: no-repeat center/cover url('../img/hero-mobile.jpg');

    &__wrapper {
      padding-left: 26px;
    }

    &__logo {
      max-width: 251px;
      width: 100%;
    }

    &__illustration {
      left: -19px;

      img {
        width: 1090px;
        margin-bottom: 0;
      }
    }
  }
}
