@use 'helpers/mixins' as *;

.main-text {
  font-size: 80px;
  line-height: 76px;
  letter-spacing: -1.6px;

  &--beige {
    color: var(--beige);
  }

  @include media(max, 1199) {
    font-size: 70px;
  }

  @include media(max, 767) {
    font-size: 60px;
  }

  @include media(max, 575) {
    font-size: 36px;
    line-height: 34px;
    letter-spacing: -0.72px;
  }
}

h3,
.h3 {
  font-size: 48px;
  line-height: 52px;
  font-weight: 400;

  @include media(max, 991) {
    font-size: 36px;
    line-height: 44px;
  }

  @include media(max, 575) {
    font-size: 24px;
    line-height: 26px;
  }
}

h4,
.h4 {
  font-size: 32px;
  font-weight: 400;
  line-height: 44px;
  letter-spacing: -0.64px;
  text-transform: uppercase;

  @include media(max, 575) {
    font-size: 18px;
    letter-spacing: -0.36px;
  }
}

.p-24 {
  font-size: 24px;
  line-height: 32px;

  @include media(max, 575) {
    font-size: 18px;
    line-height: 24px;
  }
}

.p-btn {
  font-family: var(--second-font);
  font-size: 16px;
  line-height: 27px;
  font-weight: 400;
  text-decoration-line: underline;
  color: var(--brown);

  @include media(max, 575) {
    font-size: 12px;
  }
}
