@use 'helpers/mixins' as *;

.support {
  padding: 120px 0 100px;
  overflow: hidden;

  &__wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    &::before {
      content: '';
      width: 500px;
      height: 500px;
      border-radius: 50%;
      border: 1px solid var(--light-gray);
      position: relative;
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;

    p {
      margin: 0;
      margin-bottom: 21px;
      color: var(--dark-gray);
    }

    h3 {
      line-height: 56px;
      margin: 0;
      margin-bottom: 38px;
    }
  }

  &__btn {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 13px;
    border-radius: 26px;
    border: 1px solid var(--light-gray);
    width: 254px;
    height: 52px;
    transition: var(--transition);

    &:hover {
      border: 1px solid var(--dark-gray);
    }

    p {
      margin-bottom: 0;
      color: var(--brown);
    }
  }
}

@include media(max, 575) {
  .support {
    padding: 95px 0 72px;

    &__wrapper {
      &::before {
        width: 343px;
        height: 343px;
      }
    }

    &__info {
      p {
        margin-bottom: 23px;
        font-size: 12px;
        line-height: 18px;
      }

      h3 {
        margin-bottom: 24px;
        letter-spacing: -0.48px;
        font-size: 24px;
        line-height: 26px;
      }
    }

    &__btn {
      gap: 13px;
      width: 191px;
      height: 43px;

      p {
        margin-bottom: 0;
      }

      svg {
        width: 25px;
        height: 5px;
      }
    }
  }
}
