@use 'helpers/mixins' as *;

.metaphorical__hero {
  display: flex;
  background: #f5f2ee;
  overflow: hidden;

  > div {
    width: 50%;
  }

  &-img {
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &-title {
    display: flex;
    flex-direction: column;
    gap: 124px;
    position: relative;
    z-index: 1;
    padding: 79px 65px 99px 70px;

    h1 {
      font-size: 48px;
      font-weight: 400;
      line-height: 54px;
      letter-spacing: -0.96px;
      max-width: 580px;
    }

    svg {
      position: absolute;
      top: 35px;
      left: 85px;
      z-index: -1;
    }
  }

  &-subtitle {
    display: flex;
    justify-content: flex-end;
    max-width: 580px;

    p {
      max-width: 303px;
      color: var(--brown);
    }
  }
}

.metaphorical__info {
  padding: 200px 0 180px;
  position: relative;
  overflow: hidden;

  &-illustration {
    position: absolute;
    bottom: 160px;
    left: 50%;
    transform: translateX(-50%);
  }

  &-wrapper {
    display: flex;
  }

  &-list {
    margin-right: 75px;

    ul {
      padding-left: 0;
      margin: 0;
    }

    li {
      display: flex;
      color: var(--dark-gray);
      max-width: 555px;

      &::before {
        content: '';
        display: block;
        width: 1px;
        height: 70.968px;
        transform: rotate(38.5deg);
        background: var(--light-gray);
        margin-right: 47px;
        flex-shrink: 0;
      }

      &:not(:last-child) {
        margin-bottom: 50px;
      }
    }
  }

  &-img {
    max-width: 343px;
    border-radius: 172px 172px 0px 0px;
    margin-right: 75px;
    position: relative;
    overflow: visible;
    flex-shrink: 0;

    img {
      border-radius: 172px 172px 0px 0px;
    }

    svg {
      position: absolute;
      bottom: 44px;
      left: -42px;
    }
  }

  &-text {
    color: var(--brown);
    font-size: 24px;
    font-weight: 500;
    line-height: 32px;
    text-transform: uppercase;
    max-width: 490px;
    width: 100%;
    flex-shrink: 0;

    &::first-letter {
      padding-left: 58px;
    }
  }
}

.metaphorical__about {
  margin-bottom: 180px;
  &-title {
    border-top: 1px solid var(--light-gray);
    border-bottom: 1px solid var(--light-gray);
    padding: 26px 0;

    h3 {
      letter-spacing: -0.96px;
      padding-left: 80px;
    }

    img {
      position: absolute;
      right: 45px;
      top: -50px;
      transform: rotate(9.5deg);
      border-radius: 6px;
    }
  }

  &-subtitle {
    border-bottom: 1px solid var(--light-gray);
    padding: 27px 0;

    h3 {
      padding-left: 495px;

      span {
        padding-left: 110px;
      }
    }

    h5 {
      display: none;
    }
  }

  &-images {
    position: absolute;
    top: 25px;
    left: 82px;

    > div {
      position: relative;
    }

    img {
      border-radius: 6px;

      &:first-child {
        transform: rotate(-11.5deg);
      }

      &:last-child {
        transform: rotate(9.5deg);
        position: absolute;
        top: 130px;
        left: 180px;
      }
    }
  }

  &-info {
    display: flex;
    justify-content: space-between;
    max-width: 825px;
    margin-left: auto;
    padding-right: 18px;
    margin-top: 65px;

    p {
      color: var(--dark-gray);
    }

    .about-info-left {
      max-width: 292px;

      h5 {
        color: var(--brown);
        font-size: 16px;
        font-weight: 500;
        line-height: 24px;
        text-transform: uppercase;
        margin: 0;
        margin-bottom: 33px;
      }
    }

    .about-info-right {
      max-width: 408px;

      p {
        margin-bottom: 36px;
      }

      a {
        border-radius: 26px;
        border: 1px solid var(--light-gray);
        color: var(--brown);
        font-family: var(--second-font);
        font-size: 16px;
        font-weight: 400;
        line-height: 56px;
        text-decoration-line: underline;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 408px;
        height: 52px;
        transition: var(--transition);

        &:hover {
          border-color: var(--dark-gray);
        }
      }
    }
  }

  &-info-mobile {
    display: none;
  }
}

.metaphorical__autor {
  overflow: hidden;
  &-title {
    display: flex;
    gap: 48px;
    margin-bottom: 78px;

    h3 {
      line-height: 54px;
      letter-spacing: -0.96px;
    }
  }

  &-text {
    display: flex;
    flex-direction: column;
    gap: 26px;
    color: var(--brown);
    max-width: 720px;
  }

  .swiper {
    overflow: visible;
  }

  .swiper-slide {
    width: 295px;
  }

  .autor__info-item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border-radius: 20px;
    border: 1px solid var(--light-gray);
    padding: 30px 33px 40px;
    min-height: 282px;
  }

  .autor__info-title {
    display: flex;
    flex-direction: column;
    gap: 7px;
    h5 {
      margin: 0;
      color: var(--brown);
      font-weight: 500;
      text-transform: uppercase;
    }
    p {
      color: var(--dark-gray);

      display: -webkit-box;
      -webkit-line-clamp: 4;
      -webkit-box-orient: vertical;
      overflow: hidden;
    }
  }

  .autor__email {
    color: var(--brown);
    font-family: var(--second-font);
    text-decoration-line: underline;
  }

  .autor__info-socials {
    display: flex;
    gap: 13px;
  }

  .autor__navigation {
    margin-top: 44px;
    position: relative;
    width: 100%;

    a {
      color: var(--brown);
      font-family: var(--second-font);
      text-decoration-line: underline;
    }
  }

  .navigation-arrow {
    position: absolute;
    right: 55px;
    bottom: 0;
    width: 126px;
    height: 33px;
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: space-between;
    gap: 32px;

    > div {
      position: static;
      top: 0;
      width: 47px;
      height: 32px;
      margin: 0;

      svg {
        width: 47px;
      }

      &::after {
        content: unset;
      }
    }
  }
}

@include media(max, 1399) {
  .metaphorical__hero {
    &-title {
      gap: 100px;
      padding: 70px 45px 80px;

      svg {
        left: 15px;
      }
    }
  }

  .metaphorical__info {
    &-illustration {
      bottom: 230px;
    }
    &-wrapper {
      flex-wrap: wrap;
      justify-content: flex-end;
      align-items: center;
      gap: 30px;
    }
    &-list {
      order: 1;
      flex-grow: 3;
    }
    &-img {
      order: 2;
    }
    &-text {
      order: 3;
    }
  }

  .metaphorical__about {
    &-info {
      justify-content: flex-end;
      gap: 10px;
      padding-right: 0;
    }
  }
}

@include media(max, 1199) {
  .metaphorical__hero {
    &-title {
      gap: 90px;
      padding: 70px 50px;

      h1 {
        font-size: 42px;
        line-height: 52px;
      }

      svg {
        left: 15px;
      }
    }
  }

  .metaphorical__info {
    padding: 140px 0 120px;

    &-illustration {
      bottom: 270px;
    }

    &-wrapper {
      flex-direction: column;
      justify-content: center;
      gap: 50px;
    }

    &-list {
      margin-right: 0;
    }

    &-img {
      margin-right: 0;
    }
  }

  .metaphorical__about {
    &-info {
      flex-direction: column;
      justify-content: flex-end;
      align-items: flex-end;
      gap: 12px;
      margin-top: 45px;

      .about-info-left {
        max-width: 408px;
      }
    }
  }
}

@include media(max, 991) {
  .metaphorical__hero {
    flex-direction: column-reverse;
    gap: 32px;

    > div {
      width: 100%;
    }

    &-title {
      padding: 79px 65px 99px 70px;

      svg {
        top: 16px;
        left: 50%;
        transform: translateX(-50%);
      }
    }
  }

  .metaphorical__about {
    &-title {
      h3 {
        padding-left: 0;
      }
    }

    &-subtitle {
      h3 {
        padding-left: 325px;

        span {
          padding-left: 80px;
        }
      }
    }

    &-images {
      left: 32px;
    }

    &-info {
      p {
        color: var(--dark-gray);
      }

      .about-info-left {
        max-width: 292px;
        margin-bottom: 20px;

        h5 {
          margin-bottom: 33px;
        }
      }

      .about-info-right {
        max-width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
      }
    }
  }

  .metaphorical__autor {
    &-title {
      flex-direction: column;
      gap: 32px;

      h3 {
        max-width: 560px;
      }
    }
  }
}

@include media(max, 767) {
  .metaphorical__hero {
    &-img {
      max-width: 680px;
      margin: 0 auto;
      padding: 0 40px;
    }
    &-title {
      padding: 70px 40px;
    }
  }

  .metaphorical__about {
    &-title {
      padding: 22px 0;
      h3 {
        font-size: 32px;
        line-height: 38px;
      }
      img {
        display: none;
      }
    }

    &-subtitle {
      padding: 22px 0 30px;
      h3 {
        padding-left: 0;
        font-size: 32px;
        line-height: 38px;

        span {
          padding-left: 80px;
        }
      }
    }

    &-images {
      left: 32px;
      top: 100px;

      img {
        &:first-child {
          width: 160px;
        }

        &:last-child {
          top: 110px;
          left: 135px;
          width: 100px;
        }
      }
    }

    &-info {
      margin-top: 40px;
      p {
        font-size: 12px;
        line-height: 18px;
      }

      .about-info-left {
        max-width: 205px;
        margin-bottom: 30px;

        h5 {
          font-size: 12px;
          font-weight: 500;
          line-height: 18px;
          margin-bottom: 33px;
        }
      }

      .about-info-right {
        max-width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;

        a {
          font-size: 14px;
          line-height: 56px;
          width: 360px;
          height: 48px;
        }
      }
    }
  }
}

@include media(max, 575) {
  .metaphorical__hero {
    gap: 0;
    &-img {
      padding: 0 16px;
    }

    &-title {
      gap: 14px;
      padding: 48px 16px;

      h1 {
        font-size: 36px;
        font-weight: 400;
        line-height: 34px;
        max-width: 343px;
        letter-spacing: -0.72px;
      }

      svg {
        width: 320px;
        height: 320px;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }

    &-subtitle {
      justify-content: flex-start;

      p {
        font-size: 12px;
        line-height: 18px;
      }
    }
  }

  .metaphorical__info {
    padding: 120px 0 90px;

    &-illustration {
      bottom: 100px;
    }

    &-wrapper {
      justify-content: center;
      gap: 0;
    }

    &-list {
      margin-right: 0;
      margin-bottom: 48px;

      li {
        position: relative;
        padding-left: 42px;
        font-size: 12px;
        line-height: 18px;

        &::before {
          height: 42px;
          margin-right: 47px;
          position: absolute;
          left: 13px;
          margin-right: 0;
        }

        &:not(:last-child) {
          margin-bottom: 27px;
        }
      }
    }

    &-img {
      margin-right: 0;
      max-width: 174px;
      margin-bottom: 46px;

      svg {
        bottom: -37px;
        left: -22px;
        max-width: 218px;
      }
    }

    &-text {
      font-size: 16px;
      line-height: 20px;
      font-weight: 400;

      &::first-letter {
        padding-left: 40px;
      }
    }
  }

  .metaphorical__about {
    margin-bottom: 88px;
    &-title {
      padding: 20px 0;

      h3 {
        font-size: 24px;
        line-height: 26px;
        letter-spacing: -0.48px;
        max-width: 267px;
      }
    }

    &-subtitle {
      padding: 20px 0 0;

      h3 {
        font-size: 24px;
        line-height: 26px;
        letter-spacing: -0.48px;

        span {
          padding-left: 65px;
        }
      }

      h5 {
        display: flex;
        max-width: 125px;
        margin-left: auto;
        font-size: 12px;
        font-weight: 500;
        line-height: 18px;
        margin-bottom: 33px;
        color: var(--brown);
        text-transform: uppercase;
        margin-top: 14px;
      }
    }

    &-images {
      top: 75px;
      left: 32px;

      img {
        &:first-child {
          width: 95px;
        }

        &:last-child {
          top: 50px;
          left: 79px;
        }
      }
    }

    &-info {
      display: none;
    }

    &-info-mobile {
      display: flex;
      align-items: center;
      flex-direction: column;
      margin-top: 100px;

      > div {
        display: flex;
        gap: 30px;
        margin-bottom: 30px;

        p {
          color: var(--dark-gray);
          font-size: 12px;
          line-height: 18px;
        }

        .about-info-left {
          width: 146px;
        }

        .about-info-right {
          width: 166px;
        }
      }

      a {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 309px;
        height: 43px;
        font-size: 12px;
        font-weight: 400;
        line-height: 56px;
        border-radius: 26px;
        border: 1px solid #d6d1ca;
        font-size: 12px;
        line-height: 56px;
        color: var(--brown);
        text-decoration: underline;
      }
    }
  }

  .metaphorical__autor {
    &-title {
      margin-bottom: 36px;
      gap: 34px;

      h3 {
        line-height: 26px;
        letter-spacing: -0.48px;
      }
    }

    &-text {
      gap: 22px;
      p {
        font-size: 12px;
        line-height: 18px;
      }
    }

    .swiper-slide {
      width: 166px;
    }

    .autor__info-item {
      border-radius: 16px;
      padding: 24px 13px 23px;
      min-height: 196px;
    }

    .autor__info-title {
      gap: 6px;
      h5 {
        font-size: 12px;
        line-height: 16px;
      }
      p {
        font-size: 10px;
        line-height: 14px;
      }
    }

    .autor__email {
      font-size: 10px;
      line-height: 24px;
    }

    .autor__info-socials {
      gap: 9px;
    }

    .autor__navigation {
      margin-top: 27px;

      a {
        font-size: 12px;
        line-height: 24px;
      }
    }

    .navigation-arrow {
      right: 16px;
      width: 87px;
      height: 23px;
      gap: 22px;

      > div {
        width: 32px;
        height: 22px;

        svg {
          width: 32px;
        }
      }
    }
  }
}

@include media(max, 374) {
  .metaphorical__about-images > div {
    margin-top: 20px;
  }
}
