@use 'helpers/mixins' as *;

.accordion {
  display: flex;
  flex-direction: column;

  &--border {
    &::before {
      content: '';
      display: block;
      width: 100%;
      padding-top: 25px;
      border-top: 1px solid var(--light-gray);
    }
  }

  &-item {
    transition: var(--transition);

    &:not(:first-child) {
      margin-top: 24px;
    }

    &::after {
      content: '';
      border-bottom: 1px solid var(--light-gray);
      padding-bottom: 25px;
      display: block;
      width: 100%;
    }
  }

  &__btn {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: 15px;
    width: 100%;

    &.active {
      margin-bottom: 24px;
      svg {
        transition: var(--transition);
        path {
          height: auto;

          &:last-child {
            visibility: hidden;
            opacity: 0;
            height: 0;
          }
        }
      }
    }

    p {
      color: var(--brown);
      font-size: 16px;
      font-weight: 500;
      line-height: 24px;
      text-transform: uppercase;
    }

    svg {
      width: 15px;
      height: 15px;
      margin-top: 5px;
      margin-left: 15px;
      flex-shrink: 0;
    }
  }

  &-content {
    display: none;

    &.active {
      display: block;
    }

    ol,
    ul {
      margin: 0;
      padding-left: 20px;
    }

    p,
    li {
      color: var(--brown);
    }

    li {
      list-style-type: decimal;

      &:not(:last-child) {
        margin-bottom: 12px;
      }
    }

    .dotted {
      list-style-type: disc;
    }

    p {
      &:not(:last-child) {
        margin-bottom: 24px;
      }
    }
  }
}

@include media(max, 575) {
  .accordion {
    &--border {
      &::before {
        padding-top: 20px;
      }
    }

    &-item {
      &:not(:first-child) {
        margin-top: 20px;
      }

      &::after {
        padding-bottom: 20px;
      }
    }

    &__btn {
      &.active {
        margin-bottom: 20px;
      }

      p {
        line-height: 18px;
      }

      svg {
        min-width: 11px;
        min-height: 11px;
        width: 11px;
        height: 11px;
        margin-top: 3px;
      }
    }

    &-content {
      ol,
      ul {
        margin: 0;
        padding-left: 16px;
      }

      p,
      li {
        color: var(--brown);

        font-size: 12px;
        font-weight: 400;
        line-height: 18px;
      }

      li {
        list-style-type: decimal;

        &:not(:last-child) {
          margin-bottom: 12px;
        }
      }

      .dotted {
        list-style-type: disc;
      }

      p {
        &:not(:last-child) {
          margin-bottom: 24px;
        }
      }
    }
  }
}
