@use 'helpers/mixins' as *;

.about__hero {
  background: no-repeat center/cover url(../img/about-hero.jpg);
  margin-top: 0;
  position: relative;
  z-index: 1;
  overflow: hidden;
  padding: 210px 0 72px 0;

  &-title {
    margin-bottom: 187px;

    .main-text {
      color: var(--peach);
      font-weight: 400;
    }
  }

  &-list {
    display: flex;
    justify-content: space-between;
  }

  .list-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 220px;
    min-height: 212px;
    position: relative;

    &:not(:last-child) {
      &::after {
        content: '';
        border-right: 1px solid var(--peach);
        opacity: 0.3;
        height: 100%;
        position: absolute;
        right: 0;
        margin-right: 3px;
      }
    }

    img {
      padding-top: 25px;
    }

    p {
      min-height: 96px;
      margin-top: auto;
      text-align: center;
      color: var(--peach);
      max-width: 213px;
    }
  }
}

.about__mass-media {
  padding-top: 180px;

  .main-text {
    font-weight: 400;
    margin-bottom: 76px;
  }

  .mass-media__wrapper {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 25px;
    margin-bottom: 52px;
  }

  .mass-media__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 20px;
    border: 1px solid var(--light-gray);
    min-height: 226px;
    mix-blend-mode: darken;
    position: relative;
    padding: 24px 28px 36px 24px;

    .logo {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      transition: var(--transition);
    }

    .arrow-up {
      opacity: 0;
      visibility: hidden;
      position: absolute;
      right: 24px;
      top: 24px;
      transition: var(--transition);
    }

    p {
      color: var(--brown);
      opacity: 0;
      visibility: hidden;
      transition: var(--transition);
      margin-top: auto;
    }

    &:hover {
      border: 1px solid var(--brown);
      .logo {
        transform: unset;
        position: absolute;
        left: 24px;
        top: 24px;
        max-width: 30%;
      }
      .arrow-up,
      p {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  .btn-wrapper {
    display: flex;
    justify-content: center;
    width: 100%;

    .show-more {
      display: flex;
      align-items: center;
      justify-content: center;
      color: var(--brown);
      font-family: var(--second-font);
      border-radius: 26px;
      border: 1px solid var(--light-gray);
      width: 184px;
      height: 52px;
      text-decoration: underline;
      transition: var(--transition);

      &:hover {
        border: 1px solid var(--dark-gray);
      }
    }
  }
}

.about__amora {
  padding-top: 170px;

  &-wrapper {
    max-width: 1092px;
    margin: 0 auto;
  }

  &-title {
    display: flex;
    margin-bottom: 60px;

    svg {
      margin-right: 82px;
    }

    p {
      max-width: 398px;
      color: var(--brown);
    }
  }

  &-elements {
    display: flex;
    justify-content: space-between;

    svg {
      margin-top: 90px;
    }
  }

  .element-item {
    max-width: 444px;

    .top-semicircle {
      display: flex;
      justify-content: center;
      width: 444px;
      height: 221px;
      border-radius: 1000px 1000px 0 0;
      padding-bottom: 26px;
      margin-bottom: 36px;
      position: relative;

      h3 {
        color: #faf9f8;
        margin-top: auto;
        z-index: 5;
      }
    }

    p {
      text-align: center;
      color: var(--brown);
    }
  }
}

.about__details {
  padding-top: 170px;
  padding-bottom: 190px;

  .accordion-item {
    margin-top: 0;
    box-shadow: 0px -36px 73px -50px rgba(0, 0, 0, 0.1);
    &::after {
      content: unset;
    }

    &:first-child {
      button {
        border-top: 1px solid var(--light-gray);
      }
    }

    .accordion__btn {
      padding: 13px 0;
      transition: var(--transition);
      border-bottom: 1px solid var(--light-gray);

      > div {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        opacity: 0.6;
      }

      p {
        display: flex;
        align-items: center;
        font-size: 24px;
        line-height: 24px;
        text-transform: inherit;

        svg {
          width: 6px;
          height: 6px;
          margin: 0;
          margin-right: 20px;
        }
      }

      span {
        color: var(--brown);
        font-family: var(--second-font);
      }

      &.active {
        > div {
          opacity: 1;
        }
      }
    }

    .container {
      &.active {
        .accordion-content {
          display: block;
        }
      }
    }
  }

  .main-text {
    padding: 23px 0;
    font-weight: 400;
  }

  .wrapper {
    display: flex;
    border-top: 1px solid var(--light-gray);
    border-bottom: 1px solid var(--light-gray);

    > div {
      display: flex;
    }
  }

  .left-side {
    display: flex;
    flex-direction: column;
    padding: 63px 84px 63px 100px;
    border-right: 1px solid var(--light-gray);

    img {
      max-width: 305px;
    }

    .subtitle {
      color: var(--brown);
      font-size: 24px;
      font-weight: 500;
      line-height: 26px;
      text-transform: uppercase;
      max-width: 355px;
      margin-top: 47px;
    }

    span {
      color: var(--dark-gray);
      font-style: italic;
      margin-top: 20px;
    }
  }

  .right-side {
    display: flex;
    flex-direction: column;
    gap: 24px;
    padding: 63px 84px;

    p {
      max-width: 413px;
      margin-bottom: 0;
    }
  }
}

.about__amora-is {
  padding-bottom: 180px;
  .amora-is__title {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 950px;
    margin: 0 auto;
    text-align: center;

    &::before {
      content: '';
      display: block;
      width: 1px;
      height: 65px;
      transform: rotate(38.5deg);
      background: var(--brown);
      margin-bottom: 46px;
    }

    h3 {
      line-height: 54px;
      letter-spacing: -0.96px;
    }
  }
}

.about__cosmetics {
  padding-bottom: 180px;

  &-top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 62px;

    h2 {
      font-weight: 400;
    }

    a {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 20px;
      border-radius: 26px;
      border: 1px solid var(--light-gray);
      padding: 0 24px;
      height: 52px;
      transition: var(--transition);
      color: var(--brown);
      font-family: var(--second-font);

      &:hover {
        border: 1px solid var(--dark-gray);
      }
    }
  }

  &-info {
    display: flex;
    gap: 100px;
  }

  .info-left {
    img {
      margin-bottom: 30px;
      margin-left: -55px;
    }

    p {
      color: var(--gray);
      font-size: 14px;
      line-height: 22px;
      max-width: 504px;
    }
  }

  .info-right {
    max-width: 670px;
    position: relative;

    h4 {
      font-family: var(--main-font);
      font-weight: 500;
      line-height: 39.5px;
      margin-bottom: 44px;
    }

    p {
      color: var(--brown);
    }

    .swiper {
      height: fit-content;
    }

    .navigation-wrapper {
      display: flex;
      width: 100%;
      height: 32px;
      margin-top: 120px;
      position: relative;
    }

    .swiper-pagination {
      position: absolute;
      bottom: 16px;
      top: unset;
      max-width: 409px;
    }

    .swiper-pagination-progressbar {
      background: #cdc7c1;
      height: 2px;

      &-fill {
        background: var(--brown);
      }
    }

    .arrow-navigation {
      display: flex;
      flex-direction: row-reverse;
      justify-content: space-between;
      gap: 32px;
      position: absolute;
      bottom: 0;
      right: 64px;
      width: 126px;
      height: 32px;

      > div {
        width: 47px;
        height: 32px;
        position: static;
        margin: 0;

        &::after {
          content: unset;
        }
      }
    }
  }

  .mobile-block {
    display: none;
  }
}

.about__born {
  &-wrapper {
    padding-left: 33px;
  }
  &-title {
    display: flex;
    gap: 72px;
    margin-bottom: 68px;

    span {
      color: var(--brown);
      width: 376px;
    }

    h2 {
      font-weight: 400;
      max-width: 714px;
    }
  }
  &-info {
    display: flex;
    gap: 55px;
    position: relative;
    margin-bottom: 160px;
  }
  .born-description {
    display: flex;
    gap: 72px;

    img {
      max-width: 376px;
    }

    p {
      color: var(--brown);

      &:not(:last-child) {
        margin-bottom: 24px;
      }
    }
  }
  .title-wrapper {
    position: relative;
    padding-left: 40px;

    &::before {
      content: '';
      display: flex;
      gap: 30px;
      width: 1px;
      background: var(--brown);
      height: 100%;
      position: absolute;
    }
  }

  h4 {
    margin-top: 60px;
    line-height: 38px;
    max-width: 385px;
    margin: 40px;

    span {
      padding-left: 68px;
    }
  }
  .born-idea {
    max-width: 205px;
    .title-wrapper {
      display: none;
    }
  }

  .born-info {
    display: flex;
    gap: 92px;
    margin-bottom: 180px;

    &__description {
      max-width: 496px;

      p {
        color: var(--brown);
        &:not(:last-child) {
          margin-bottom: 24px;
        }
      }

      strong {
        font-weight: 600;
      }
    }
    &__photos {
      display: flex;
      gap: 62px;
      max-width: 678px;

      div {
        &:first-child {
          max-width: 247px;
        }
        &:last-child {
          max-width: 369px;
        }
      }
    }
  }

  &-search {
    display: flex;
    flex-direction: column;
    gap: 118px;
  }

  &-img {
    display: flex;
    align-items: center;
    img {
      max-width: 606px;
    }

    .mobile {
      display: none;
    }

    > div {
      position: relative;
      &::after {
        content: '';
        position: absolute;
        left: 0;
        top: -94px;
        display: block;
        width: 378px;
        height: 378px;
        border-radius: 100%;
        border: 1px solid #dfd8cf;
        z-index: -1;
      }

      svg {
        position: absolute;
        left: 88px;
        top: -8px;
      }
    }
    h4 {
      margin: 0;
      margin-left: 164px;
    }
  }

  .born-search__text {
    display: flex;
    gap: 90px;
    padding-left: 74px;

    .left,
    .right {
      display: flex;
      gap: 17px;

      img {
        width: 29px;
        height: 29px;
      }

      p {
        color: var(--brown);
      }

      strong {
        font-weight: 600;
      }
    }

    .left {
      max-width: 376px;
    }
    .right {
      max-width: 415px;
    }
  }

  &-overview {
    padding: 200px 0 180px;
  }

  .born-overview {
    &__wrapper {
      display: flex;
      gap: 162px;
    }

    &__info {
      max-width: 530px;

      .main-text {
        font-weight: 400;
        margin-bottom: 82px;
      }

      p {
        color: var(--brown);
        max-width: 394px;
        margin-left: auto;
        margin-bottom: 24px;
      }

      .profile {
        margin-top: 56px;
        display: flex;
        gap: 27px;
        margin-bottom: 21px;

        img {
          width: 68px;
          height: 68px;
          border-radius: 50%;
        }

        &-info {
          display: flex;
          flex-direction: column;

          &__name {
            color: var(--brown);
            font-family: var(--second-font);
            font-size: 24px;
            line-height: 39.5px;
            letter-spacing: -0.48px;
            text-transform: uppercase;
          }

          &__post {
            color: #b1aca6;
          }
        }
      }

      .painting {
        max-width: 175px;
        margin-left: auto;
      }
    }

    &__photos {
      max-width: 569px;
    }

    &__mobile {
      display: none;
    }
  }
}

