      padding: 0;

      .logo {
        max-width: 63%;
      }

      .arrow-up,
      p {
        display: none;
      }

      &:hover {
        .logo {
          left: 50%;
          top: 50%;
          transform: translate(-50%, -50%);
          max-width: 50%;
        }
      }
    }

    .btn-wrapper {
      .show-more {
        width: 133px;
        height: 43px;
        font-size: 12px;
      }
    }
  }

  .about__amora {
    padding-top: 100px;

    &-title {
      gap: 22px;
      margin-bottom: 52px;

      svg {
        margin-right: 0;
        width: 256px;
        height: 64px;
      }

      p {
        width: 201px;
        font-size: 12px;
        line-height: 18px;
      }
    }

    &-elements {
      svg {
        margin: 32px 0;
        width: 28px;
        height: 28px;
      }
    }

    .element-item {
      display: flex;
      flex-direction: column;
      align-items: center;

      .top-semicircle {
        max-width: 186px;
        height: 93px;
        border-radius: 1000px 1000px 0 0;
        padding-bottom: 14px;
        margin-bottom: 30px;

        h3 {
          font-size: 32px;
          letter-spacing: -0.64px;
        }
      }

      p {
        font-size: 12px;
        line-height: 18px;
        max-width: 300px;
      }
    }
  }

  .about__details {
    padding: 100px 0;

    .accordion-item {
      &:not(:first-child) {
        margin-top: 0;
      }

      .accordion__btn {
        padding: 13px 0;
        margin-bottom: 0;

        p {
          font-size: 14px;
          line-height: 24px;
          max-width: 260px;
          font-weight: 400;

          svg {
            width: 4px;
            height: 4px;
            min-width: 4px;
            min-height: 4px;
            margin-right: 10px;
          }
        }

        span {
          padding: 10px 0;
        }
      }
    }

    .accordion-content {
      h2 {
        font-size: 36px;
        line-height: 34px;
        letter-spacing: -0.72px;
      }
    }

    .main-text {
      padding: 55px 0 42px;
      font-weight: 400;
      font-size: 36px;
      line-height: 34px;
      letter-spacing: -0.72px;
    }

    .wrapper {
      border-top: none;
      border-bottom: 1px solid var(--light-gray);
      padding-bottom: 55px;

      > div {
        display: flex;
      }
    }

    .left-side {
      flex-direction: column;
      padding: 0;
      padding-bottom: 28px;
      gap: 0;
      img {
        max-width: 305px;
      }

      .subtitle {
        font-size: 16px;
        font-weight: 500;
        line-height: 20px;
        margin-top: 42px;
        margin-bottom: 0;
      }

      span {
        width: 100%;
      }
    }

    .right-side {
      padding: 0;
      gap: 18px;

      p {
        font-size: 12px;
        line-height: 18px;
        margin: 0;
      }
    }
  }

  .about__amora-is {
    padding-bottom: 100px;
    .amora-is__title {
      &::before {
        height: 46px;
        margin-bottom: 34px;
      }

      h3 {
        font-size: 24px;
        line-height: 26px;
        letter-spacing: -0.48px;
      }
    }
  }

  .about__cosmetics {
    padding-bottom: 100px;
    &-top {
      margin-bottom: 34px;
      a {
        display: none;
      }
    }

    &-info {
      gap: 54px;
    }

    .info-left {
      img {
        margin-left: -16px;
        max-width: 359px;
      }
    }

    .info-right {
      max-width: 100%;

      h4 {
        font-size: 16px;
        line-height: 20px;
        margin-bottom: 26px;
      }

      p {
        font-size: 12px;
        line-height: 18px;
      }

      .navigation-wrapper {
        margin-top: 50px;
        height: 51px;
      }

      .swiper-pagination {
        max-width: 100%;
        top: 0;
      }

      .arrow-navigation {
        right: 0;
        width: 87px;
        height: 22px;
        gap: 22px;

        > div {
          width: 32px;
          height: 22px;
        }
      }
    }

    .mobile-block {
      margin-top: 54px;
      gap: 29px;

      p {
        font-size: 12px;
        line-height: 18px;
      }

      a {
        height: 43px;
        padding: 0 24px;
        font-size: 12px;
        gap: 13px;

        svg {
          width: 25px;
          height: 5px;
        }
      }
    }
  }

  .about__born {
    &-title {
      flex-direction: row;
      gap: 37px;
      margin-bottom: 27px;

      span {
        font-size: 12px;
        line-height: 18px;
        max-width: 82px;
      }
    }

    &-info {
      margin-bottom: 54px;
      gap: 70px;
    }

    .born-description {
      flex-direction: row;
      align-items: flex-start;
      gap: 36px;

      img {
        max-width: 105px;
      }

      p {
        font-size: 12px;
        line-height: 18px;

        &:not(:last-child) {
          margin-bottom: 18px;
        }
      }
    }

    .born-idea {
      align-items: flex-start;
      gap: 60px;

      img {
        max-width: 82px;
      }

      .title-wrapper {
        padding-left: 0;

        h4 {
          margin-top: 0;
          padding-left: 21px;
          line-height: 24px;

          span {
            display: block;
            width: 170px;
          }
        }

        span {
          padding-left: 36px;
        }
      }
    }

    .born-info {
      gap: 44px;
      align-items: flex-end;
      margin-bottom: 80px;

      &__description {
        max-width: 100%;

        p {
          font-size: 12px;
          line-height: 18px;
          color: var(--brown);
          &:not(:last-child) {
            margin-bottom: 18px;
          }
        }
      }
      &__photos {
        gap: 24px;

        div {
          &:first-child {
            max-width: 95px;
          }
          &:last-child {
            max-width: 141px;
          }
        }
      }
    }

    &-img {
      justify-content: center;
      > div {
        svg {
          width: 20px;
          height: 17px;
          left: 38px;
          top: 5px;
        }
        &::after {
          top: -21px;
          width: 187px;
          height: 187px;
        }
      }

      .mobile {
        max-width: 107px;
      }

      h4 {
        font-size: 18px;
        line-height: 24px;
        margin-left: 65px;
        max-width: 234px;
      }
    }

    &-search {
      gap: 54px;
    }

    &-overview {
      padding: 80px 0 100px;
    }

    .born-overview {
      &__wrapper {
        display: flex;
        gap: 0;
      }

      &__info {
        .main-text {
          margin-bottom: 42px;
        }

        .description {
          display: none;
        }
      }

      &__photos {
        margin-bottom: 42px;
      }

      &__mobile {
        display: block;

        p {
          margin-bottom: 20px;
        }

        .profile {
          margin-top: 44px;
          display: flex;
          align-items: center;
          gap: 17px;

          img {
            width: 56px;
            height: 56px;
            border-radius: 50%;
          }

          &-info {
            display: flex;
            flex-direction: column;
            &__name {
              color: var(--brown);
              font-family: var(--second-font);
              line-height: 39.5px;
              letter-spacing: -0.32px;
              text-transform: uppercase;
            }
            &__post {
              color: #b1aca6;
            }
          }
        }

        .painting {
          margin-top: 11px;
          max-width: 87px;
          margin-left: auto;

          svg {
            max-width: 87px;
          }
        }
      }
    }
  }
}

@include media(max, 374) {
  .about__hero {
    .list-item {
      min-width: 150px;

      &:nth-child(odd) {
        width: unset;
      }
    }
  }
}
