@use 'helpers/mixins' as *;

.product-page {
  .feedback {
    padding-bottom: 0;
  }
}

.product {
  padding: 70px 0 150px;

  &__wrapper {
    display: grid;
    align-items: flex-start;
    grid-template-columns: 507px 1fr;
    gap: 94px;
  }

  &-image {
    position: sticky;
    top: 15px;
    img {
      max-height: 515px;
      width: 100%;
      height: 100%;
      object-fit: cover;
      aspect-ratio: 1;
      @include media(max, 1399) {
        max-height: 505px;
      }
      @include media(max, 1199) {
        max-height: 325px;
      }
      @include media(max, 767) {
        max-height: 515px;
      }
    }
  }

  --swiper-navigation-sides-offset: 36px;

  .navigation-image {
    > div {
      width: 47px;
      height: 32px;

      &::after {
        content: unset;
      }
    }
  }

  .swiper-pagination {
    bottom: 16px;
  }

  .swiper-pagination-bullet {
    background: #faf9f8;
    transition: var(--transition);
  }

  .swiper-pagination-bullet-active {
    width: 34px;
    border-radius: 4px;
  }

  &__details {
    h3 {
      margin-bottom: 30px;
      line-height: 54px;
    }

    .subtext {
      margin-bottom: 30px;
    }

    p,
    span {
      color: var(--brown);
    }

    .price {
      margin-right: 24px;
      font-size: 24px;
      font-weight: 500;
      line-height: 24px;
    }

    .size {
      color: var(--gray);
    }

    .buy {
      margin-top: 38px;
    }

    .markets {
      margin-top: 18px;
    }
  }
}

.usage {
  margin-bottom: 195px;

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 54px;
  }

  &-title {
    position: relative;
    max-width: 828px;
    width: 100%;

    h2 {
      font-weight: 400;
    }

    .category {
      border-radius: 27px;
      background: #ece9e7;
      padding: 7px 22px;
      color: var(--brown);
      position: absolute;
      top: 14px;
      left: 380px;
    }
  }

  .link-video {
    display: flex;
    align-items: center;
    gap: 12px;
    color: var(--brown);
    font-family: var(--second-font);
    line-height: 56px;
    text-decoration-line: underline;
  }

  &-steps {
    position: relative;
    &::after {
      content: '';
      border-bottom: 1px solid #cdc7c1;
      display: block;
      position: absolute;
      top: 276px;
      width: 100%;
      z-index: -1;
    }
  }

  .step-item {
    display: flex;
    flex-direction: column;
    align-items: center;

    h3,
    p {
      color: #b1aca6;
      transition: var(--transition);
      display: -webkit-box;
      -webkit-line-clamp: 5;
      -webkit-box-orient: vertical;
      overflow: hidden;
      @include media(max, 575) {
        -webkit-line-clamp: 4;
      }
    }

    h3 {
      margin-bottom: 30px;
    }

    p {
      margin-bottom: 53px;
      text-align: center;
    }

    .navigation {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 44px;
      max-width: 124px;
      width: 100%;
      position: relative;
      margin-top: auto;
      &-btns {
        background: #faf9f8;
        opacity: 0;
        visibility: hidden;
        transition: var(--transition);
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 5px;
        border-radius: 40px;
        border: 1px solid #cdc7c1;
        height: 44px;
        max-width: 124px;
        width: 0;

        &--single {
          max-width: 74px;
        }

        > div {
          position: static;
          margin-top: 0;
          width: 100%;

          svg {
            width: 22px;
          }

          &::after {
            content: unset;
          }
        }

        &:not(.navigation-btns--single) {
          .swiper-button-prev {
            padding-left: 7px;
          }

          .swiper-button-next {
            padding-right: 7px;
          }
        }
      }

      .circle {
        width: 22px;
        height: 22px;
        background: #faf9f8;
        border: 1px solid #cdc7c1;
        border-radius: 50%;
        transition: var(--transition);
        position: absolute;
      }
    }
  }

  .swiper-slide {
    width: 330px;
    min-height: 298px;

    &-active {
      .circle {
        opacity: 0;
        visibility: hidden;
        width: 0;
        height: 0;
      }

      .navigation-btns {
        opacity: 1;
        visibility: visible;
        width: 100%;
      }

      h3,
      p {
        color: var(--brown);
      }
    }
  }
}

.product-video {
  margin-bottom: 180px;

  h2 {
    font-weight: 400;
    margin-bottom: 54px;
  }

  .video__wrapper {
    max-width: 1440px;
    width: 100%;
    margin: 0 auto;
    position: relative;

    video {
      width: 100%;
      max-height: 600px;
      vertical-align: top;
      object-fit: cover;
    }

    .play-button {
      display: flex;
      align-items: center;
      position: absolute;
      padding-left: 29px;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 74px;
      height: 74px;
      border: 1px solid #e4dcc7;
      border-radius: 50%;
      background: rgba($color: #e4dcc7, $alpha: 0.3);
      transition: var(--transition);

      &:hover {
        background: rgba($color: #e4dcc7, $alpha: 0.2);
      }
    }
  }

  .video-desc {
    display: flex;
    max-width: 1440px;
    margin: 0 auto;

    &__image {
      display: flex;
      justify-content: center;
      align-items: center;
      background: #ece9e7;
      max-width: 610px;
      width: 100%;

      img {
        width: 260px;
        height: 314px;
        object-fit: cover;
      }
    }

    .properties {
      background: #f5f2ee;
      width: 100%;
      padding: 173px 100px;

      ul {
        margin: 0;
        padding: 0;
      }

      li {
        color: var(--brown);
        font-family: var(--second-font);
        font-size: 32px;
        line-height: 54px;
        &:not(:last-child) {
          margin-bottom: 10px;
        }
        &::before {
          content: '~';
          margin-right: 36px;
        }
      }
    }
  }
}

@include media(max, 1399) {
  .product__wrapper {
    gap: 40px;
    grid-template-columns: 500px 1fr;
  }
  .product__details {
    max-width: 550px;
    width: 100%;
  }
}

@include media(max, 1199) {
  .product {
    &__wrapper {
      gap: 40px;
      grid-template-columns: 320px 1fr;
    }
  }

  .usage {
    .link-video {
      white-space: nowrap;
    }
  }

  .product-video {
    .video-desc {
      &__image {
        max-width: 420px;
      }

      .properties {
        padding: 100px 50px;

        li {
          color: var(--brown);
          font-family: var(--second-font);
          font-size: 28px;
          line-height: 40px;
        }
      }
    }
  }
}

@include media(max, 991) {
  .product {
    &__wrapper {
      gap: 40px;
      grid-template-columns: 320px 1fr;
    }

    .product__details {
      h3 {
        font-size: 30px;
        line-height: 42px;
      }
    }

    .markets--large {
      .markets__link {
        width: 64px;
        height: 44px;
        border-radius: 50px;
      }

      p {
        display: none;
      }
    }
  }

  .usage {
    &__top {
      flex-direction: column;
      align-items: flex-end;
      gap: 20px;
    }

    &-steps {
      &::after {
        top: 278px;
      }
    }

    .category {
      left: 0;
      top: 182px;
    }

    .step-item {
      h3 {
        font-size: 48px;
        line-height: 54px;
        letter-spacing: -0.96px;
      }
    }
  }
}

@include media(max, 767) {
  .product {
    &__wrapper {
      gap: 40px;
      grid-template-columns: 1fr;
    }

    &-image {
      max-width: 500px;
      width: 100%;
      position: relative;
    }
  }

  .usage {
    margin-bottom: 140px;
    &-steps {
      &::after {
        top: 278px;
      }
    }
    &-title {
      .main-text {
        font-size: 48px;
        line-height: 56px;
      }
      .category {
        top: 142px;
      }
    }
    .swiper-slide {
      min-height: 217px;
    }
  }

  .product-video {
    margin-bottom: 120px;
    .main-text {
      font-size: 48px;
      line-height: 56px;
      margin-bottom: 16px;
    }
    .video-desc {
      &__image {
        img {
          width: 160px;
          height: 195px;
        }
      }

      .properties {
        padding: 50px 20px 46px;
        li {
          font-size: 16px;
          line-height: 22px;

          &::before {
            margin-right: 10px;
          }
        }
      }
    }
  }
}

@include media(max, 575) {
  .product {
    padding: 20px 0 120px;
    &__wrapper {
      gap: 42px;
      grid-template-columns: 100%;
    }

    &-image {
      max-width: 575px;
      width: 100%;
    }

    .product__details {
      h3 {
        font-size: 24px;
        line-height: 26px;
        letter-spacing: -0.48px;
        margin-bottom: 20px;
      }
      .subtext {
        font-size: 12px;
        line-height: 18px;
        margin-bottom: 30px;
      }
      .price {
        font-size: 16px;
        font-weight: 500;
        line-height: 20px;
        margin-right: 12px;
      }
      .size {
        font-size: 12px;
        line-height: 18px;
      }
      .buy {
        font-size: 12px;
        line-height: 18px;
        margin: 0;
      }
      .markets-wrapper {
        margin-top: 28px;
        margin-bottom: 76px;
        display: flex;
        align-items: center;
        gap: 26px;

        .markets--large {
          margin: 0;
        }
      }
    }

    --swiper-navigation-sides-offset: 24px;

    .navigation-image {
      > div {
        width: 31px;
        height: 21px;
      }
    }

    .swiper-button-next,
    .swiper-button-prev {
      margin: 0;
    }

    .swiper-pagination-bullet {
      width: 4px;
      height: 4px;
    }

    .swiper-pagination-bullet-active {
      width: 20px;
      height: 4px;
      border-radius: 4px;
    }
  }

  .usage {
    margin-bottom: 120px;
    &__top {
      gap: 30px;
      margin-bottom: 36px;
    }
    &-title {
      .main-text {
        font-size: 24px;
        font-weight: 400;
        line-height: 26px;
        letter-spacing: -0.48px;
        max-width: 335px;
      }
      .category {
        top: 79px;
        font-size: 12px;
        line-height: 24px;
        padding: 4px 22px;
      }
    }
    .link-video {
      font-size: 12px;
      font-weight: 400;
      line-height: 24px;
      text-decoration-line: underline;

      svg {
        width: 24px;
        height: 24px;
      }
    }
    &-steps {
      &::after {
        top: 193px;
      }
    }
    .step-item {
      h3 {
        margin-bottom: 17px;
      }
      p {
        font-size: 12px;
        font-weight: 400;
        line-height: 18px;
        margin-bottom: 28px;
      }
    }
  }

  .product-video {
    margin-bottom: 95px;
    .main-text {
      font-size: 24px;
      line-height: 26px;
      letter-spacing: -0.48px;
      margin-bottom: 28px;
    }
    .video-desc {
      &__image {
        max-width: 140px;

        img {
          width: 80px;
          height: 112px;
        }
      }
      .properties {
        padding: 46px 17px 41px;

        li {
          font-size: 12px;
          line-height: 16px;
        }
      }
    }
  }
}
