@use 'helpers/mixins' as *;

.modal {
  --overlay-color: rgba(0, 0, 0, 0.5);
  --crossHeight: 20px;
  --max-width: 878px;

  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--overlay-color);
  z-index: 100;
  overflow-x: hidden;
  overflow-y: auto;
  display: none;
  animation: fadeOut 0.25s both ease;

  &__overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(--overlay-color);
  }

  &--open {
    animation: fadeIn 0.25s both ease;
    display: block;
  }

  &__content {
    border-radius: 20px;
    background: #faf9f8;
    overflow: hidden;
    width: 100%;
    position: relative;
    visibility: hidden;
    opacity: 0;
    transform: translateY(-15%);
    transition: 0.25s ease;
    cursor: default;
    max-width: var(--max-width);
    margin: 0 auto;
    margin-top: 50px;

    &--open {
      visibility: visible;
      opacity: 1;
      transform: translateY(0);
    }
  }

  &__close {
    border: 0;
    padding: 0;
    background: 0;
    font-size: var(--crossHeight);
    font-weight: bold;
    width: var(--crossHeight);
    height: var(--crossHeight);
    line-height: var(--crossHeight);
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    right: 22px;
    top: 22px;
    cursor: pointer;
    transition: var(--transition);
  }

  &__body {
    display: flex;
    gap: 52px;
    padding: 47px 53px 48px 42px;

    img {
      max-width: 300px;
      max-height: 410px;
      border-radius: 10px;
      border: 1px solid #d6d1ca;
    }
    &-img {
      position: relative;
      align-self: flex-start;
      margin: 0 auto;
    }
    &-text {
      display: flex;
      flex-direction: column;

      p {
        color: var(--brown);
        margin-bottom: 24px;
      }

      .cardAuthor {
        font-style: italic;
        color: var(--dark-gray);
        margin-bottom: 32px;
      }

      .cardCreator {
        margin-bottom: 0;
      }

      .creator {
        margin-bottom: 23px;
        position: relative;
        padding-top: 24px;

        a {
          color: var(--brown);
        }

        &::before {
          content: '';
          position: absolute;
          top: 0;
          display: block;
          width: 100%;
          height: 1px;
          background: #d6d1ca;
        }
      }

      .socials {
        display: flex;
        gap: 13px;
      }
    }
  }
}

@include media(max, 991) {
  .modal {
    --max-width: 650px;

    &__body {
      gap: 32px;

      &-img {
        img {
          max-width: 260px;
        }
      }
    }
  }
}

@include media(max, 767) {
  .modal {
    --max-width: 450px;

    &__body {
      flex-direction: column;
      align-items: center;
      gap: 22px;
    }
  }
}

@include media(max, 575) {
  .modal {
    --max-width: 343px;

    &__body {
      flex-direction: column;
      align-items: center;
      gap: 30px;
      padding: 47px 27px 43px;

      &-img {
        img {
          max-width: 225px;
        }
      }

      &-text {
        font-size: 12px;
        line-height: 18px;
      }

      .cardText {
        margin-bottom: 20px;
      }
    }
  }
}
