.list-reset {
	margin: 0;
	padding: 0;
	list-style-type: none;
}

.btn-reset {
	padding: 0;
	margin: 0;
	background: 0;
	border: 0;
	color: inherit;
	text-align: left;
	appearance: none;
  cursor: pointer;
}

.link-reset {
	text-decoration: none;
	color: inherit;
	appearance: none;
}

.input-reset {
	padding: 0;
	border: 0;
	appearance: none;
	box-shadow: none;
	border-radius: 0;
}

.visually-hidden {
	clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}
