@use 'helpers/mixins' as *;

.ritual {
  padding-bottom: 180px;

  &__list {
    &-item {
      position: relative;
      display: flex;
      gap: 67px;

      &:not(:last-child) {
        margin-bottom: 18px;
        padding-bottom: 70px;

        &::after {
          content: '';
          position: absolute;
          bottom: 0;
          left: 41px;
          background-image: url('../img/dot-pc.svg');
          background-repeat: repeat-y;
          display: block;
          width: 6px;
          height: calc(100% - 88px);
        }
      }
    }
  }

  .list-number {
    position: relative;

    > div {
      display: flex;
      align-items: center;
      justify-content: center;
      min-width: 88px;
      height: 88px;
      border: 1px solid var(--brown);
      border-radius: 100px;
      color: var(--brown);
      font-family: var(--second-font);
      font-size: 38px;

      &::before {
        content: '';
        border-bottom: 1px solid var(--brown);
        width: 46px;
        position: absolute;
        right: -46px;
        top: 40px;
      }
    }
  }

  &__element {
    display: flex;
    width: 100%;

    &-details {
      max-width: 370px;
      width: 100%;
      margin-right: 45px;
      padding-top: 24px;

      h3 {
        letter-spacing: -0.96px;
        margin-bottom: 23px;
      }

      .mini-title {
        font-weight: 500;
        text-transform: uppercase;
        margin-bottom: 39px;
        max-width: 285px;
      }

      p {
        color: var(--brown);
        margin-bottom: 20px;
      }

      span {
        color: var(--gray);
        margin-bottom: 4px;
      }
    }

    &-info {
      display: flex;
      width: 100%;
      border-radius: 158px 0px 0px 0px;
      border: 1px solid var(--light-gray);
      background: var(--white);
    }

    .left-info {
      display: flex;
      flex-direction: column;
      position: relative;
      max-width: 341px;
      width: 100%;
      border-right: 1px solid var(--light-gray);

      .info__image {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        height: 100%;
      }

      .side-image {
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        left: 4px;
        top: 14px;
        width: 86px;
        height: 86px;
        border: 1px solid var(--light-gray);
        border-radius: 100px;
        background-color: #fff;

        > img {
          width: 64px;
          height: 64px;
          border-radius: 100px;
        }
      }

      .open-description {
        display: none;
        align-items: center;
        justify-content: center;
        border: 1px solid var(--light-gray);
        border-radius: 100px;
        width: 26px;
        height: 26px;
        position: absolute;
        bottom: 12px;
        right: 12px;

        &.active {
          svg {
            > line {
              &:last-child {
                transition: var(--transition);
                opacity: 0;
                visibility: hidden;
              }
            }
          }
        }
      }
    }

    .link-more {
      display: flex;
      align-items: center;
      justify-content: center;
      color: var(--brown);
      font-family: var(--second-font);
      text-decoration-line: underline;
      padding: 17px 0 21px 0;
      position: relative;

      &::before {
        content: '';
        border-top: 1px solid var(--light-gray);
        position: absolute;
        top: 0;
        width: 100%;
      }
    }

    .mobile-link {
      display: none;
    }

    .right-info {
      padding: 37px 30px 36px 45px;
    }

    .element__description {
      max-width: 332px;
      width: 100%;
      color: var(--brown);

      p {
        &:not(:last-child) {
          margin-bottom: 26px;
        }
      }

      span {
        font-weight: 600;
      }
    }
  }
}

@include media(max, 1399) {
  .ritual {
    &__element {
      &-info {
        flex-direction: column;
        height: fit-content;
      }

      .left-info {
        max-width: 100%;
        border-right: none;

        .open-description {
          display: flex;
        }
      }

      .link-more {
        display: none;
      }

      .mobile-link {
        display: none;
      }

      .right-info {
        padding: 0;

        a {
          display: flex;
        }
      }

      .element__description {
        border-top: 1px solid var(--light-gray);
        max-width: 100%;
        opacity: 0;
        visibility: hidden;
        height: 0;
        padding: 0 40px;
        transition: var(--transition);

        &.active {
          display: block;
          opacity: 1;
          visibility: visible;
          height: fit-content;
          padding: 35px 40px 40px 40px;
        }
      }
    }
  }
}

@include media(max, 991) {
  .ritual {
    &__element {
      &-info {
        border-radius: 85px 0px 0px 0px;
      }
      .left-info {
        max-width: 100%;

        .side-image {
          left: 0;
          top: 0;
          width: 60px;
          height: 60px;

          > img {
            width: 45px;
            height: 45px;
          }
        }

        .open-description {
          display: flex;
        }
      }
    }
  }
}

@include media-mm(767, 991) {
  .ritual__list-item {
    gap: 35px;
  }
  .ritual__element-info {
    max-width: 240px;
  }
  .ritual__element-details {
    margin-right: 25px;
  }
  .ritual .list-number > div::before {
    width: 20px;
    right: -20px;
  }
}

@include media(max, 767) {
  .ritual {
    &__list {
      &-item {
        gap: 50px;

        &:not(:last-child) {
          margin-bottom: 18px;
          padding-bottom: 50px;

          &::after {
            left: 28px;
            height: calc(100% - 66px);
          }
        }
      }
    }

    .list-number {
      > div {
        min-width: 66px;
        height: 66px;
        font-size: 28px;

        &::before {
          top: 33px;
        }
      }
    }

    &__element {
      flex-direction: column;
      &-details {
        max-width: 100%;
        margin-right: 0;
        padding-top: 16px;
        margin-bottom: 18px;

        h3 {
          margin-bottom: 18px;
          max-width: 254px;
        }

        .mini-title {
          margin-bottom: 18px;
        }
      }
    }
  }
}

@include media(max, 575) {
  .ritual {
    padding-bottom: 94px;

    &__list {
      &-item {
        gap: 45px;

        &:not(:last-child) {
          margin-bottom: 18px;
          padding-bottom: 64px;

          &::after {
            left: 20px;
            height: calc(100% - 44px);
            background-image: url('../img/dot-mobile.svg');
          }
        }
      }
    }

    .list-number {
      > div {
        min-width: 44px;
        height: 44px;
        font-size: 18px;
        line-height: 24px;

        &::before {
          top: 22px;
          width: 30px;
          right: -30px;
        }
      }
    }

    &__element {
      flex-direction: column;
      &-details {
        padding-top: 9px;
        margin-bottom: 25px;

        h3 {
          margin-bottom: 13px;
        }

        .mini-title {
          margin-bottom: 23px;
          font-size: 12px;
          font-weight: 500;
          line-height: 18px;
          max-width: 225px;
          width: 100%;
        }

        span,
        p {
          display: block;
          font-size: 12px;
          line-height: 18px;
          max-width: 225px;
          width: 100%;
        }

        p {
          margin-bottom: 12px;
        }
      }

      &-info {
        max-width: 205px;
      }

      .link-more {
        padding: 6px 0 10px;
        font-size: 12px;
        line-height: 24px;
      }

      .left-info {
        max-width: 100%;
        max-height: 225px;

        .info__image {
          height: 225px;
        }

        .main-image {
          min-height: 163px;
        }

        .side-image {
          left: -3px;
          top: 9px;
          width: 52px;
          height: 52px;

          > img {
            width: 38px;
            height: 39px;
          }
        }
      }

      .element__description {
        padding: 0 12px 0 30px;

        &.active {
          padding: 25px 12px 23px 30px;
        }

        p {
          font-size: 12px;
          line-height: 18px;
          &:not(:last-child) {
            margin-bottom: 22px;
          }
        }
      }
    }
  }
}
