@use 'helpers/mixins' as *;

.cards {
  padding-bottom: 196px;
  &__title {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 756px;
    margin: 0 auto;
    margin-bottom: 60px;

    svg {
      margin-bottom: 36px;
    }

    h3 {
      max-width: 666px;
      margin-bottom: 36px;
    }

    p {
      text-align: center;
      color: var(--brown);
    }
  }
  &__grid {
    max-width: 1206px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 60px;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 30px;
  }
  &__text {
    max-width: 756px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    color: var(--dark-gray);
    margin-bottom: 28px;
  }
  &__button {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 26px;
    border: 1px solid var(--light-gray);
    height: 52px;
    padding-left: 32px;
    padding-right: 32px;
    transition: var(--transition);
    color: var(--brown);
    font-family: var(--second-font);
    text-decoration: underline;
    margin-left: auto;
    margin-right: auto;
    &:hover {
      border-color: var(--dark-gray);
    }
  }
}

.card {
  border-radius: 20px;
  border: 1px solid #d8d8cb;
  overflow: hidden;
  position: relative;
  &__counter {
    width: 23px;
    height: 23px;
    border-radius: 50%;
    border: 1px solid #626753;
    background-color: #d8d8cb;
    color: #626753;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
    position: absolute;
    bottom: 10px;
    right: 10px;
  }
}

@include media(max, 1199) {
  .cards {
    &__grid {
      grid-template-columns: repeat(5, 1fr);
    }
  }
}

@include media(max, 991) {
  .cards {
    &__title {
      h3 {
        max-width: 500px;
      }
    }
    &__grid {
      grid-template-columns: repeat(4, 1fr);
    }
  }
}

@include media(max, 767) {
  .cards {
    &__grid {
      grid-template-columns: repeat(3, 1fr);
    }
  }
}

@include media(max, 575) {
  .cards {
    padding-bottom: 120px;
    &__title {
      margin-bottom: 30px;
      svg {
        height: 46px;
        margin-bottom: 30px;
      }

      h3 {
        margin-bottom: 20px;
        max-width: 333px;
      }

      p {
        font-size: 12px;
        line-height: 18px;
      }
    }
    &__grid {
      grid-template-columns: repeat(4, 1fr);
      gap: 12px 14px;
      margin-bottom: 30px;
    }
    &__text {
      font-size: 12px;
      line-height: 18px;
      margin-bottom: 22px;
    }
    &__button {
      font-size: 12px;
      height: 43px;
    }
  }

  .card {
    border-radius: 10px;
    &:nth-child(17),
    &:nth-child(18) {
      display: none;
    }
  }
}
