@use 'helpers/mixins' as *;

.catalog-list {
  padding: 145px 0 180px;

  .info__wrapper {
    display: flex;
    flex-direction: column;
    gap: 38px;
    max-width: 1186px;
    margin: 0 auto;
  }

  .info-item {
    display: flex;
    border: 1px solid #d6d1ca;
    border-radius: 20px;
  }

  .info-image {
    padding: 30px 33px;
    border-right: 1px solid #d6d1ca;

    img {
      max-width: 254px;
      object-fit: cover;
    }
  }

  .info-details {
    position: relative;
    padding: 47px 48px 37px 40px;
    width: 100%;
    overflow: hidden;

    h4 {
      padding-bottom: 40px;
      margin-bottom: 50px;
      position: relative;
      &::after {
        content: '';
        border-bottom: 1px solid var(--light-gray);
        width: 100vw;
        position: absolute;
        @include center-x;
        bottom: 0;
      }
    }

    p {
      margin-bottom: 60px;
      color: var(--brown);
      width: 100%;
    }
  }

  .read-more {
    display: flex;
    align-items: center;
    gap: 16px;
    color: var(--brown);
    font-family: var(--second-font);
    text-decoration-line: underline;
  }

  .category {
    padding: 7px 22px;
    line-height: 24px;
    color: var(--brown);
    background: #ece9e7;
    border-radius: 22px;
    position: absolute;
    top: 47px;
    right: 48px;
  }
}

@include media(max, 1199) {
  .catalog-list {
    .info-details {
      padding: 85px 48px 37px 40px;

      p {
        margin-bottom: 40px;
      }
    }

    .category {
      top: 30px;
      left: 40px;
      right: unset;
    }

    .info-image {
      padding: 36px 33px;
    }
  }
}

@include media(max, 991) {
  .catalog-list {
    .info-details {
      padding: 85px 20px 20px 20px;

      h4 {
        font-size: 28px;
        line-height: 40px;
        padding-bottom: 30px;
        margin-bottom: 20px;
      }

      p {
        margin-bottom: 32px;
      }
    }

    .category {
      left: 20px;
    }

    .info-image {
      display: flex;
      padding: 36px 30px;
    }
  }
}

@include media(max, 767) {
  .catalog-list {
    .info-item {
      flex-direction: column;
    }

    .info-image {
      border-right: 0;

      img {
        max-width: 100%;
      }
    }
  }
}

@include media(max, 575) {
  .catalog-list {
    padding: 120px 0;
    .info__wrapper {
      max-width: 350px;
    }

    .info-image img {
      aspect-ratio: 1;
    }

    .info-details {
      padding: 80px 20px 20px 20px;

      h4 {
        font-size: 24px;
        line-height: 26px;
        letter-spacing: -0.48px;
        position: static;
        margin-bottom: 0;
        padding-bottom: 14px;
        &::after {
          top: 0;
          bottom: auto;
        }
      }

      p {
        margin-bottom: 14px;
        font-size: 12px;
        line-height: 18px;
      }

      .read-more {
        font-size: 12px;
        line-height: 24px;
        gap: 8px;

        svg {
          width: 24px;
        }
      }
    }

    .info-item {
      flex-direction: column;
    }

    .info-image {
      border-right: 0;
      padding: 30px 25px;

      img {
        max-width: 100%;
      }
    }

    .category {
      padding: 4px 22px;
      font-size: 12px;
      line-height: 24px;
      top: 27px;
    }
  }
}
