@use '../helpers/mixins' as *;

.header {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  z-index: 11;
  border-bottom: 1px solid rgba(239, 224, 208, 0.3);

  &--light {
    position: relative;
    background: #f5f2ee;
    border-bottom: 1px solid #cdc7c1;

    &--transparent {
      background: transparent;
    }

    .header {
      border-bottom: 0;
    }

    .header__link {
      color: var(--brown);

      &.active {
        border-bottom: 1px solid var(--brown);
        padding-bottom: 24px;
      }
    }

    .header__menu {
      span {
        background: var(--brown);
      }

      &.active {
        span {
          background: var(--peach);
        }
      }
    }

    .header__socials-links {
      a {
        opacity: 1;
        transition: var(--transition);

        svg {
          rect {
            stroke: var(--brown);
          }
          path {
            fill: var(--brown);
          }
          circle {
            &[stroke] {
              stroke: var(--brown);
            }
            &[fill] {
              fill: var(--brown);
            }
          }
        }

        &:hover {
          opacity: 1;
        }
      }
    }
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 0 19px 0;
  }

  &__menu {
    display: none;
    z-index: 21;

    span {
      transition: var(--transition);
    }

    &.active {
      span {
        &:last-child {
          margin-left: 10px;
        }
      }
    }

    span {
      display: block;
      width: 28px;
      height: 1px;
      background-color: var(--peach);

      &:first-child {
        margin-bottom: 6px;
      }
    }
  }

  &__list {
    display: flex;
    gap: 42px;
  }

  &__link {
    color: var(--peach);
    opacity: 0.6;
    transition: var(--transition);

    &.active {
      opacity: 1;
      border-bottom: 1px solid var(--peach);
      padding-bottom: 24px;
    }

    &:hover {
      opacity: 1;
    }
  }

  &__socials-links {
    display: flex;
    gap: 12px;
    z-index: 21;

    a:hover svg rect,
    a:hover svg circle,
    a:hover svg path {
      opacity: 1;
      transition: var(--transition);
    }
  }
}

@include media(max, 991) {
  .header {
    &--light {
      .header__link {
        color: #faf9f8;
        &.active {
          border-bottom: 0;
          padding-bottom: 0;
        }
      }

      .header__socials-links {
        a {
          opacity: 1;
        }
      }
    }

    &__menu {
      display: block;
    }

    &__nav {
      visibility: hidden;
      opacity: 0;
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 20;
      transition: 0.5s;
      background-color: var(--dark-gray);
      overflow: hidden;

      &.active {
        visibility: visible;
        opacity: 1;

        + .header__socials-links {
          a {
            opacity: 1;
            transition: var(--transition);

            svg {
              rect {
                stroke: var(--peach);
              }
              path {
                fill: var(--peach);
              }
              circle {
                &[stroke] {
                  stroke: var(--peach);
                }
                &[fill] {
                  fill: var(--peach);
                }
              }
            }

            &:hover {
              opacity: 1;
            }
          }
        }
      }
    }

    &__list {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 8px;
      margin-top: 67px;
      padding: 0 16px;
      padding-top: 80px;
      border-top: 1px solid rgba(239, 224, 208, 0.3);
    }

    &__link {
      color: #faf9f8;
      opacity: 1;
      font-family: var(--second-font);
      font-size: 24px;
      font-weight: 400;
      line-height: 34px;
      letter-spacing: -0.48px;
      text-transform: uppercase;

      &.active {
        color: var(--peach);
        border-bottom: 0;
        padding-bottom: 0;
      }
    }

    &__socials-links {
      svg rect,
      svg circle,
      svg path {
        opacity: 1;
        transition: var(--transition);
      }
    }
  }
}

@include media(max, 575) {
  .header__list {
    align-items: flex-start;
    margin-top: 58px;
  }

  .header__socials-links {
    align-items: center;

    a {
      display: flex;
      align-items: center;
    }
    svg {
      width: 19px;
      height: 19px;
    }
  }
}
