@use 'helpers/mixins' as *;

.face {
  padding: 169px 0 194px;

  &__wrapper {
    position: relative;
  }

  &__title {
    display: flex;
    align-items: flex-start;
    width: 100%;

    img {
      margin-right: 31px;
      padding-top: 8px;
    }

    h1 {
      margin: 0;
      font-weight: 400;
    }

    span {
      &:nth-child(3) {
        margin-left: 115px;
      }
      &:nth-child(5) {
        margin-left: 245px;
      }
    }
  }

  &__subtitle {
    display: flex;
    align-items: flex-end;
    flex-direction: row-reverse;
    position: absolute;
    top: 8px;
    right: 107px;

    .huge-img {
      max-width: 319px;
    }

    p {
      margin: 0;
      padding-bottom: 36px;
      text-transform: uppercase;
      margin-right: 189px;

      span {
        color: var(--brown);

        &:nth-child(1) {
          padding-left: 45px;
        }

        &:nth-child(5) {
          padding-left: 103px;
        }
      }
    }
  }

  &__options {
    display: flex;
    align-items: flex-end;
    margin-top: 120px;
    margin-left: 73px;

    .small-img {
      margin-right: 81px;
      max-width: 226px;
    }

    &-item {
      display: flex;
      padding-bottom: 5px;

      p {
        display: flex;
        align-items: center;
        margin: 0;
        color: var(--dark-gray);
        text-transform: lowercase;

        &:not(:last-child) {
          margin-right: 80px;
        }

        &::before {
          content: '';
          display: block;
          width: 1px;
          height: 65px;
          transform: rotate(38.5deg);
          background: var(--light-gray);
          margin-right: 30px;
        }
      }
    }
  }
}

@include media(max, 1199) {
  .face {
    &__title {
      width: 100%;

      span {
        &:nth-child(3) {
          margin-left: 52px;
        }
        &:nth-child(5) {
          margin-left: 110px;
        }
      }
    }

    &__subtitle {
      align-items: flex-end;
      position: absolute;
      right: 0;
      top: 0;
      margin-bottom: 40px;

      img {
        width: 290px;
      }

      p {
        margin: 0;
        padding: 0;
        font-size: 18px;
        margin-right: 61px;
        padding-bottom: 10px;

        span {
          color: var(--brown);

          &:nth-child(1) {
            padding-left: 30px;
          }

          &:nth-child(5) {
            padding-left: 67px;
          }
        }
      }
    }

    &__options {
      align-items: flex-start;
      margin-top: 210px;

      .scale-img {
        margin-right: 40px;
      }

      &-item {
        flex-direction: row;
        gap: 44px;

        p {
          &:not(:last-child) {
            margin-right: 0;
          }
        }
      }
    }
  }
}

@include media(max, 991) {
  .face {
    padding: 94px 0 92px;

    &__title {
      width: 100%;
      margin-bottom: 32px;
      justify-content: center;

      img {
        margin-right: 16px;
        padding-top: 4px;
        width: 20px;
        height: 18px;
      }

      span {
        &:nth-child(3) {
          margin-left: 52px;
        }
        &:nth-child(5) {
          margin-left: 110px;
        }
      }
    }

    &__subtitle {
      align-items: center;
      justify-content: center;
      position: unset;

      img {
        width: 220px;
      }

      p {
        margin: 0;
        padding: 0;
        margin-right: 61px;
      }
    }

    &__options {
      justify-content: center;
      margin: 0;

      .scale-img {
        margin-right: 60px;
        width: 198px;
      }

      &-item {
        display: flex;
        flex-direction: column;
        gap: 20px;
        padding-bottom: 0;

        p {
          margin: 0;

          &:not(:last-child) {
            margin-right: 0;
          }

          &::before {
            width: 1px;
            height: 42px;
            margin: 0;
            margin-right: 25px;
          }
        }
      }
    }
  }
}

@include media(max, 575) {
  .face {
    padding: 94px 0 92px;

    &__title {
      span {
        &:nth-child(3) {
          margin-left: 52px;
        }
        &:nth-child(5) {
          margin-left: 110px;
        }
      }
    }

    &__subtitle {
      img {
        width: 108px;
      }

      p {
        margin: 0;
        padding: 0;
        font-size: 16px;
        line-height: 20px;
        margin-right: 61px;

        span {
          color: var(--brown);

          &:nth-child(1) {
            padding-left: 30px;
          }

          &:nth-child(5) {
            padding-left: 67px;
          }
        }
      }
    }

    &__options {
      .small-img {
        margin-right: 40px;
        width: 108px;
      }

      &-item {
        gap: 24px;

        p {
          margin: 0;
          font-size: 12px;
          line-height: 18px;
        }
      }
    }
  }
}
