@use 'helpers/mixins' as *;

.qr__hero {
  padding-top: 114px;
  padding-bottom: 335px;
  position: relative;
  overflow: hidden;

  &-wrapper {
    display: flex;
    gap: 65px;
    padding-left: 13px;
  }

  &-title {
    max-width: 740px;
    h1 {
      font-weight: 400;
      margin-bottom: 33px;
    }

    p {
      color: var(--brown);
      max-width: 305px;
      margin-left: auto;
      margin-right: 85px;
      text-transform: uppercase;
    }
  }

  &-img {
    position: relative;

    img {
      width: 400px;
      height: 400px;
    }

    .hero-text {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 223px;
      height: 223px;
      border: 1px solid var(--light-gray);
      border-radius: 50%;
      background: #faf9f8;
      position: absolute;
      bottom: -100px;
      right: -70px;

      p {
        color: var(--brown);
        font-style: italic;
        max-width: 166px;
        text-align: center;
      }
    }
  }

  svg {
    position: absolute;
    top: -114px;
    left: -55px;
    z-index: -1;
  }
}

.qr__resources {
  padding-bottom: 220px;
  overflow: hidden;

  &-title {
    display: flex;
    flex-direction: column;
    gap: 4px;
    margin-left: 111px;
    margin-bottom: 96px;

    h2 {
      font-weight: 400;
      max-width: 763px;
    }
  }

  &-text {
    display: flex;
    gap: 75px;
    margin-left: auto;
    margin-right: 111px;
    color: var(--dark-gray);

    .left-block {
      max-width: 338px;
    }
    .right-block {
      max-width: 215px;
    }
  }

  &-info {
    display: flex;
    flex-direction: column;
    position: relative;
    margin-bottom: 120px;

    .resource-img {
      max-width: 909px;
      margin-bottom: 95px;
    }

    .quote {
      color: var(--brown);
      max-width: 570px;
      margin-bottom: 32px;
      font-size: 24px;
      line-height: 32px;
      text-transform: uppercase;
    }

    p {
      padding-left: 114px;
      max-width: 540px;
      color: var(--dark-gray);
    }

    .resource-illustration {
      position: absolute;
      right: -200px;
      top: 113px;
      max-width: 803px;
    }
  }

  &-ritual {
    .swiper {
      overflow: visible;
    }
    .swiper-slide {
      background: #f5f2ee;
      border-radius: 20px;
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 35px 40px 35px 35px;
      min-height: 410px;
      max-width: 318px;

      p {
        display: flex;
        gap: 18px;
        color: var(--brown);
        max-width: 200px;

        &::before {
          content: '';
          display: block;
          flex-shrink: 0;
          width: 10px;
          height: 10px;
          background: var(--brown);
          border-radius: 50%;
          margin-top: 7px;
        }
      }

      > div {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 24px;
        margin-top: auto;

        img {
          max-width: fit-content;
        }

        a {
          text-decoration: underline;
          font-family: var(--second-font);
          color: var(--brown);
        }
      }
    }
  }
}

.qr__phrase {
  padding-bottom: 195px;
  position: relative;
  overflow: hidden;

  .illustration {
    position: absolute;
    top: 60px;
    left: 50%;
    transform: translateX(-50%);
  }

  &-wrapper {
    display: flex;
    justify-content: space-between;
    gap: 85px;
  }

  .phrase__left {
    max-width: 244px;
    color: var(--dark-gray);

    span {
      padding-left: 75px;
    }
  }

  .phrase__right {
    display: flex;
    flex-direction: column;
    gap: 70px;

    &-title {
      max-width: 551px;

      h3 {
        font-weight: 400;
        position: relative;

        img {
          position: absolute;
          top: 94px;
          right: 0;
          width: 30px;
          height: 30px;
        }
      }

      span {
        padding-left: 85px;
      }
    }

    &-text {
      display: flex;
      gap: 86px;
      color: var(--dark-gray);

      p {
        max-width: 403px;
      }

      a {
        text-decoration: underline;
        font-weight: 600;
      }
    }
  }
}

.qr__help {
  padding-bottom: 150px;

  &-wrapper {
    display: flex;
    align-items: center;
    gap: 86px;
    position: relative;
  }

  &-left {
    padding-left: 118px;

    &::before {
      content: '';
      display: block;
      width: 500px;
      height: 500px;
      border: 1px solid var(--light-gray);
      border-radius: 50%;
      position: absolute;
      top: 0;
      left: -446px;
      z-index: -1;
    }
    .title {
      color: var(--brown);
      font-size: 24px;
      font-weight: 500;
      line-height: 32px;
      text-transform: uppercase;
      margin-bottom: 39px;
      max-width: 513px;
    }
    .text {
      color: var(--dark-gray);
      padding-left: 54px;
    }
  }
  &-right {
    position: relative;
    padding-right: 112px;
    width: 613px;

    &::after {
      content: '';
      display: block;
      width: 500px;
      height: 500px;
      border: 1px solid var(--light-gray);
      border-radius: 50%;
      position: absolute;
      top: 0;
      right: 0;
      z-index: -1;
    }

    .support {
      padding: 0;
    }

    .support__wrapper {
      border-radius: 50%;
      &::before {
        background: #f5f2ee;
        border-radius: 50%;
        border: none;
      }
    }

    h3 {
      font-size: 32px;
      line-height: 40px;
      letter-spacing: -0.64px;
      margin-bottom: 28px;
    }
  }
}

.qr__join {
  padding-bottom: 170px;
  max-width: 1440px;
  margin: 0 auto;

  &-wrapper {
    display: flex;
  }

  &-img {
    border-radius: 0px 97px 0px 0px;
    max-width: 566px;

    img {
      border-radius: 0px 97px 0px 0px;
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }

  &-info {
    border-top: 1px solid var(--light-gray);
    border-left: 1px solid var(--light-gray);
    border-radius: 90px 0 0 0;
    padding: 85px 55px 70px;

    .top-text {
      margin-bottom: 24px;
    }

    p {
      color: var(--dark-gray);
      max-width: 720px;
    }

    .title {
      color: var(--brown);
      margin-bottom: 24px;
      font-size: 24px;
      font-weight: 500;
      line-height: 32px;
      text-transform: uppercase;
    }

    .socials {
      display: flex;
      gap: 24px;
      margin-top: 43px;
    }
  }

  .socials-mobile {
    display: none;
  }
}

@include media(max, 1399) {
  .qr__hero {
    &-title {
      max-width: 600px;
    }
    &-img {
      .hero-text {
        right: -40px;
      }
    }
  }

  .qr__resources {
    &-title {
      gap: 35px;
    }

    &-text {
      margin-left: 0;
    }

    &-info {
      .resource-illustration {
        right: -340px;
        top: 130px;
      }
    }
  }

  .qr__help {
    &-wrapper {
      gap: 56px;
    }

    &-left {
      .text {
        padding-left: 0;
      }
    }

    &-right {
      padding-right: 46px;
    }
  }
}

@include media(max, 1199) {
  .qr__hero {
    padding-bottom: 335px;

    &-wrapper {
      gap: 35px;
      padding-left: 0;
    }

    &-title {
      max-width: 500px;

      p {
        margin-right: 0;
      }
    }

    &-img {
      img {
        width: 370px;
        height: 370px;
      }

      .hero-text {
        bottom: -90px;
        right: -40px;
      }
    }

    svg {
      top: -125px;
      left: -245px;
    }
  }

  .qr__resources {
    &-info {
      .resource-illustration {
        right: -275px;
        top: 200px;
        max-width: 580px;
      }
    }
  }

  .qr__phrase {
    .phrase__right {
      gap: 60px;

      &-text {
        gap: 40px;

        p {
          max-width: 270px;
        }
      }
    }
  }

  .qr__help {
    &-wrapper {
      flex-direction: column;
    }

    &-left {
      padding-left: 0;
      max-width: 613px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      text-align: center;

      &::before {
        content: unset;
      }
    }

    &-right {
      padding-right: 0;
    }
  }

  .qr__join {
    &-img {
      max-width: 350px;
    }
  }
}

@include media(max, 991) {
  .qr__hero {
    padding-bottom: 285px;

    &-wrapper {
      flex-direction: column;
    }

    &-title {
      max-width: 100%;
    }

    &-img {
      margin-top: -110px;

      .hero-text {
        bottom: -110px;
        left: 190px;
      }
    }

    svg {
      top: -120px;
      left: -455px;
    }
  }

  .qr__resources {
    &-title {
      gap: 35px;
      margin-left: 0;
    }

    &-text {
      margin-left: 0;
    }

    &-info {
      p {
        padding-left: 0;
      }
      .resource-illustration {
        right: -155px;
        top: 200px;
        max-width: 480px;
      }
    }
  }

  .qr__phrase {
    .phrase__right {
      gap: 60px;

      &-text {
        flex-direction: column;
        gap: 20px;

        p {
          max-width: 100%;
        }
      }
    }
  }

  .qr__join {
    &-img {
      max-width: 280px;
    }
  }
}

@include media(max, 767) {
  .qr__hero {
    padding-bottom: 285px;

    &-wrapper {
      flex-direction: column;
    }

    &-title {
      max-width: 100%;
    }

    &-img {
      margin-top: 0;
    }

    svg {
      top: 0;
    }
  }

  .qr__resources {
    &-title {
      gap: 35px;
      margin-left: 0;

      h2 {
        font-size: 52px;
        line-height: 66px;
      }
    }

    &-text {
      margin-right: 0;
    }
  }

  .qr__phrase {
    &-wrapper {
      flex-direction: column;
      gap: 35px;
    }
    .phrase__right {
      gap: 60px;

      &-text {
        flex-direction: column;
        gap: 20px;

        p {
          max-width: 100%;
        }
      }
    }
  }

  .qr__help {
    &-right {
      &::after {
        content: unset;
      }
    }
  }

  .qr__join {
    &-info {
      padding: 50px 45px;
    }
  }
}

@include media(max, 575) {
  .qr__hero {
    padding-top: 48px;
    padding-bottom: 193px;

    &-wrapper {
      gap: 36px;
    }

    &-title {
      h1 {
        margin-bottom: 24px;
      }

      p {
        font-size: 12px;
        line-height: 18px;
        max-width: 215px;
      }
    }

    &-img {
      img {
        width: 290px;
        height: 290px;
      }

      .hero-text {
        width: 163px;
        height: 163px;
        bottom: -74px;
        right: 0;
        left: unset;

        p {
          max-width: 126px;
          font-size: 12px;
          line-height: 18px;
        }
      }
    }

    svg {
      top: -135px;
      left: -615px;
    }
  }

  .qr__resources {
    padding-bottom: 120px;

    &-title {
      gap: 25px;
      margin-bottom: 36px;

      h2 {
        font-size: 24px;
        line-height: 26px;
        letter-spacing: -0.48px;
        max-width: 343px;
      }
    }

    &-text {
      flex-direction: column;
      gap: 20px;

      .left-block,
      .right-block {
        max-width: 324px;
      }
    }

    &-info {
      margin-bottom: 90px;

      .resource-img {
        margin-bottom: 43px;
      }

      .quote {
        margin-bottom: 27px;
        font-size: 12px;
        font-weight: 500;
        line-height: 18px;
        max-width: 165px;
      }

      p {
        max-width: 324px;
        font-size: 12px;
        line-height: 18px;
      }

      .resource-illustration {
        right: -115px;
        top: 124px;
        max-width: 276px;
      }
    }

    &-ritual {
      .swiper {
        overflow: visible;
      }
      .swiper-slide {
        border-radius: 16px;
        padding: 21px 17px 30px 13px;
        min-height: 290px;
        max-width: 225px;

        &:not(:nth-child(2)) {
          img {
            max-width: 68%;
          }
        }

        p {
          gap: 10px;
          font-size: 12px;
          line-height: 18px;
          max-width: 170px;

          &::before {
            width: 6px;
            height: 6px;
            margin-top: 5px;
          }
        }

        > div {
          gap: 18px;

          img {
            max-width: 55%;
          }

          a {
            font-size: 12px;
            line-height: 24px;
          }
        }
      }
    }
  }

  .qr__phrase {
    padding-bottom: 120px;
    .illustration {
      height: 177px;
      object-fit: cover;
    }
    &-wrapper {
      flex-direction: column;
      align-items: center;
      gap: 28px;
    }

    .phrase__left {
      max-width: 185px;
      font-size: 12px;
      line-height: 18px;
      margin-right: auto;

      span {
        padding-left: 52px;
      }
    }

    .phrase__right {
      gap: 28px;

      &-title {
        max-width: 551px;

        h3 {
          max-width: 300px;
          margin-left: 42px;
          letter-spacing: -0.48px;

          img {
            position: absolute;
            top: -72px;
            right: 0;
            width: 30px;
            height: 30px;
          }
        }

        span {
          padding-left: 85px;
        }
      }

      &-text {
        flex-direction: row;
        gap: 30px;
        font-size: 12px;
        line-height: 18px;

        p {
          max-width: 154px;
        }

        a {
          text-decoration: underline;
          font-weight: 600;
        }
      }
    }
  }

  .qr__help {
    padding-bottom: 100px;
    &-wrapper {
      gap: 48px;
    }
    &-left {
      text-align: unset;
      .title {
        font-size: 12px;
        line-height: 18px;
        margin-bottom: 18px;
      }
      .text {
        font-size: 12px;
        line-height: 18px;
      }
    }
  }

  .qr__join {
    padding-bottom: 100px;

    &-img {
      border-radius: 0px 42px 0px 0px;
      max-width: 110px;

      img {
        border-radius: 0px 42px 0px 0px;
        max-width: 110px;
      }
    }

    &-info {
      border-radius: 42px 0 0 0;
      padding: 34px 16px 17px 27px;
      width: 100%;

      .top-text {
        margin-bottom: 24px;
      }

      p {
        font-size: 12px;
        line-height: 18px;
        max-width: 249px;
      }

      .title {
        font-size: 12px;
        line-height: 18px;
      }

      .text {
        display: none;
      }

      .socials {
        display: none;
      }
    }

    .socials-mobile {
      display: flex;
      flex-direction: column;
      gap: 22px;
      margin-top: 37px;

      p {
        color: var(--dark-gray);
        font-size: 12px;
        line-height: 18px;
      }

      svg {
        width: 28px;
        height: 28px;
      }
    }
  }
}
