@use 'helpers/mixins' as *;

.footer {
  padding-bottom: 57px;

  &__wrapper {
    display: flex;
    justify-content: space-between;
    border-top: 1px solid var(--light-gray);
    padding-top: 53px;
  }

  &__nav {
    display: flex;
    justify-content: space-between;
    max-width: 579px;
    width: 100%;
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 20px;
    max-width: 262px;
    color: var(--brown);
    line-height: 28px;

    li {
      &:last-child {
        line-height: 24px;
      }
    }
  }

  &__link {
    transition: var(--transition);

    &:hover {
      color: #80786f;
    }
  }

  &__socials {
    display: flex;
    gap: 12px;
    height: fit-content;

    a:hover svg rect,
    a:hover svg circle,
    a:hover svg path,
    a:hover svg g {
      opacity: 1;
      transition: var(--transition);
    }
  }
}

@include media(max, 1199) {
  .footer__nav {
    max-width: 440px;
  }
}

@include media(max, 991) {
  .footer__wrapper {
    flex-direction: column;
  }

  .footer__logo {
    display: none;
  }

  .footer__nav {
    max-width: 100%;
    margin-bottom: 40px;
  }
}

@include media(max, 575) {
  .footer {
    padding-bottom: 52px;

    &__wrapper {
      padding-top: 39px;
    }

    &__nav {
      max-width: 342px;
      gap: 15px;
      margin-bottom: 43px;
    }

    &__list {
      font-size: 12px;
      line-height: 28px;
      gap: 12px;
      max-width: 165px;

      li {
        &:last-child {
          line-height: 16px;
        }
      }
    }
  }
}
