@use 'helpers/mixins' as *;

.container {
  --gap: 16px;
  width: 100%;
  padding-right: var(--gap);
  padding-left: var(--gap);
  margin-right: auto;
  margin-left: auto;
  position: relative;
  @include media(min, 576) {
    max-width: 540px;
  }
  @include media(min, 768) {
    max-width: 720px;
  }
  @include media(min, 992) {
    max-width: 960px;
  }
  @include media(min, 1200) {
    max-width: 1440px;
    --gap: 55px;
  }
}
