@use 'helpers/mixins' as *;

.location {
  max-width: 1440px;
  width: 100%;
  margin: 0 auto;
  overflow: hidden;

  .location__catalog-title {
    margin-bottom: 35px;
  }

  &__wrapper {
    display: flex;
    margin-bottom: 90px;

    .location-left-img {
      border-radius: 0px 97px 0px 0px;
      max-width: 566px;
      width: 100%;

      > img {
        height: 100%;
        width: 100%;
        object-fit: cover;
      }
    }
  }

  &__elements {
    position: relative;
  }

  &__content {
    display: flex;
    flex-direction: column;
    width: 100%;
    border-top: 1px solid var(--light-gray);
    border-left: 1px solid var(--light-gray);
    border-radius: 90px 0 0 0;
    padding: 67px 20px 35px 79px;

    h3 {
      margin-bottom: 37px;
    }

    .subtext {
      color: #80786f;
      max-width: 358px;
      margin-left: 224px;
    }

    .bold-text {
      font-weight: 500;
    }
  }

  .locationSwiper {
    overflow: visible;
  }

  .swiper-button-next,
  .swiper-button-prev {
    content: '';
  }

  .swiper-slide {
    width: 426px;
  }

  &__navigation {
    margin-bottom: 47px;

    p {
      max-width: 843px;
      color: var(--brown);
    }
  }

  .navigation-arrow {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 134px;
    position: absolute;
    top: 14px;
    right: 0;

    > div {
      position: static;
      margin: 0;
      width: 51px;
      height: 20px;
      transition: var(--cubick);
      &::after {
        content: unset;
      }
    }
  }

  &__info {
    display: flex;

    &-item {
      border: 1px solid var(--light-gray);
      border-radius: 20px;
      min-height: 176px;
      display: flex;
      align-items: center;
      gap: 22px;
      padding: 0 31px;

      > img {
        border-radius: 80px;
      }
    }

    .info-desc {
      display: flex;
      flex-direction: column;
      gap: 12px;
      color: var(--brown);

      > div {
        display: flex;
        align-items: center;
        gap: 9px;
        font-family: var(----main-font);
        font-size: 12px;
        line-height: 16px;
      }

      h5 {
        margin: 0;
        font-weight: 500;
        font-size: 16px;
        line-height: 20px;
      }
    }
  }
}

.markets {
  display: flex;
  gap: 11px;
  margin-top: 47px;
  margin-bottom: 60px;

  &--large {
    .markets__link {
      width: 200px;
      height: 60px;
    }
  }

  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5px;
    border: 1px solid var(--light-gray);
    border-radius: 50px;
    width: 200px;
    height: 52px;
    color: var(--brown);
    font-size: 14px;
    font-weight: 500;
    line-height: 24px;
    transition: var(--transition);

    img {
      max-width: 30px;
    }

    &:hover {
      border-color: var(--dark-gray);
    }
  }
}

@include media(max, 1199) {
  .location {
    &__wrapper {
      .location-left-img {
        max-width: 305px;
      }
    }

    &__elements {
      padding-bottom: 64px;
    }

    .navigation-arrow {
      bottom: 0;
      top: unset;
      right: 0;
    }

    &__content {
      padding: 40px 20px 35px 40px;

      .subtext {
        margin: 0;
      }
    }
  }

  .markets {
    flex-wrap: wrap;
  }
}

@include media(max, 991) {
  .location {
    &__wrapper {
      .location-left-img {
        max-width: 280px;
      }
    }
  }
}

@include media(max, 767) {
  .location {
    &__wrapper {
      .location-left-img {
        max-width: 180px;
      }
    }

    &__content {
      h3 {
        margin-bottom: 18px;
      }
    }
  }

  .markets {
    margin-top: 22px;
    &__link {
      font-size: 12px;
      line-height: 18px;
      width: 160px;
      height: 44px;
    }
  }
}

@include media(max, 575) {
  .location {
    .location__catalog-title {
      margin-bottom: 32px;
    }

    &__wrapper {
      margin-bottom: 60px;

      .location-left-img {
        max-width: 83px;
        border-radius: 0px 42px 0px 0px;
      }
    }

    &__elements {
      padding-bottom: 49px;
    }

    &__content {
      padding: 34px 18px 0 27px;
      border-radius: 42px 0 0 0;

      h3 {
        letter-spacing: -0.48px;
        margin-bottom: 18px;
      }

      .subtext {
        max-width: 256px;
      }

      br {
        display: none;
      }

      .subtext {
        margin: 0;
        font-size: 12px;
        line-height: 18px;
      }

      .bold-text {
        font-weight: 400;
      }
    }

    &__navigation {
      flex-direction: column;
      align-items: flex-end;
      margin-bottom: 26px;

      p {
        max-width: 332px;
        font-size: 12px;
        line-height: 18px;
      }
    }

    .swiper-slide {
      width: 297px;
    }

    .navigation-arrow {
      width: 87px;

      > div {
        width: 32px;
        height: 22px;
      }
    }

    &__info {
      &-item {
        border-radius: 16px;
        min-height: 122px;
        gap: 15px;
        padding: 0 18px;

        > img {
          border-radius: 80px;
          width: 68px;
        }
      }

      .info-desc {
        gap: 8px;

        > div {
          gap: 7px;
          font-size: 10px;
          line-height: 14px;
        }

        h5 {
          font-size: 12px;
          line-height: 16px;
          width: 155px;
        }
      }
    }
  }

  .markets {
    margin-top: 18px;
    &__link {
      width: 64px;
      height: 44px;
      p {
        display: none;
      }
      img {
        width: 25px;
        height: 25px;
      }
    }
  }
}
