:root {
  --main-font: "Montserrat", sans-serif;
  --second-font: "Tenor Sans", sans-serif;
  --font-size: 16px;
  --line-height: 24px;
  --transition: 0.25s ease;
  --brown: #645140;
  --dark-gray: #80786f;
  --gray: #b1aca6;
  --light-gray: #d6d1ca;
  --beige: #d7bca3;
  --peach: #efe0d0;
  --white: #f5f3f2;
}

.fancybox__container {
  --fancybox-bg: rgb(0 0 0 / 60%);
}

.main-text {
  font-size: 80px;
  line-height: 76px;
  letter-spacing: -1.6px;
}
.main-text--beige {
  color: var(--beige);
}
@media screen and (max-width: 1199px) {
  .main-text {
    font-size: 70px;
  }
}
@media screen and (max-width: 767px) {
  .main-text {
    font-size: 60px;
  }
}
@media screen and (max-width: 575px) {
  .main-text {
    font-size: 36px;
    line-height: 34px;
    letter-spacing: -0.72px;
  }
}

h3,
.h3 {
  font-size: 48px;
  line-height: 52px;
  font-weight: 400;
}
@media screen and (max-width: 991px) {
  h3,
  .h3 {
    font-size: 36px;
    line-height: 44px;
  }
}
@media screen and (max-width: 575px) {
  h3,
  .h3 {
    font-size: 24px;
    line-height: 26px;
  }
}

h4,
.h4 {
  font-size: 32px;
  font-weight: 400;
  line-height: 44px;
  letter-spacing: -0.64px;
  text-transform: uppercase;
}
@media screen and (max-width: 575px) {
  h4,
  .h4 {
    font-size: 18px;
    letter-spacing: -0.36px;
  }
}

.p-24 {
  font-size: 24px;
  line-height: 32px;
}
@media screen and (max-width: 575px) {
  .p-24 {
    font-size: 18px;
    line-height: 24px;
  }
}

.p-btn {
  font-family: var(--second-font);
  font-size: 16px;
  line-height: 27px;
  font-weight: 400;
  text-decoration-line: underline;
  color: var(--brown);
}
@media screen and (max-width: 575px) {
  .p-btn {
    font-size: 12px;
  }
}

.tac {
  text-align: center;
}

.ttu {
  text-transform: uppercase;
}

.animate {
  animation-duration: 1s;
  animation-fill-mode: both;
}

.animate-from-left {
  opacity: 0;
  transform: translateX(-100px);
}

.animate-from-right {
  opacity: 0;
  transform: translateX(100px);
}

.animate-from-top {
  opacity: 0;
  transform: translateY(50px);
}

.scale-img {
  overflow: hidden;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
* {
  box-sizing: border-box;
}
*:before {
  box-sizing: border-box;
}
*:after {
  box-sizing: border-box;
}

.page {
  scroll-behavior: smooth;
}

.page__body {
  font-family: var(--main-font);
  font-size: var(--font-size);
  line-height: var(--line-height);
  height: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: #faf9f8;
}
.page__body.active {
  overflow: hidden;
}

.page-main {
  overflow: hidden;
}

.page-footer {
  margin-top: auto;
}

img {
  max-width: 100%;
  height: auto;
}

.img-fluid {
  max-width: 100%;
  height: auto;
}

img,
svg,
iframe {
  vertical-align: top;
}

::placeholder {
  opacity: 1;
}

::selection {
  background-color: #000;
  color: #fff;
}

h1,
h2,
h3,
h4 {
  font-family: var(--second-font);
  color: #645140;
  text-transform: uppercase;
  margin: 0;
}

p {
  margin: 0;
}

li {
  list-style-type: none;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.btn-reset {
  padding: 0;
  margin: 0;
  background: 0;
  border: 0;
  color: inherit;
  text-align: left;
  appearance: none;
  cursor: pointer;
}

.link-reset {
  text-decoration: none;
  color: inherit;
  appearance: none;
}

.input-reset {
  padding: 0;
  border: 0;
  appearance: none;
  box-shadow: none;
  border-radius: 0;
}

.visually-hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

.container {
  --gap: 16px;
  width: 100%;
  padding-right: var(--gap);
  padding-left: var(--gap);
  margin-right: auto;
  margin-left: auto;
  position: relative;
}
@media screen and (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}
@media screen and (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}
@media screen and (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}
@media screen and (min-width: 1200px) {
  .container {
    max-width: 1440px;
    --gap: 55px;
  }
}

.accordion {
  display: flex;
  flex-direction: column;
}
.accordion--border::before {
  content: "";
  display: block;
  width: 100%;
  padding-top: 25px;
  border-top: 1px solid var(--light-gray);
}
.accordion-item {
  transition: var(--transition);
}
.accordion-item:not(:first-child) {
  margin-top: 24px;
}
.accordion-item::after {
  content: "";
  border-bottom: 1px solid var(--light-gray);
  padding-bottom: 25px;
  display: block;
  width: 100%;
}
.accordion__btn {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 15px;
  width: 100%;
}
.accordion__btn.active {
  margin-bottom: 24px;
}
.accordion__btn.active svg {
  transition: var(--transition);
}
.accordion__btn.active svg path {
  height: auto;
}
.accordion__btn.active svg path:last-child {
  visibility: hidden;
  opacity: 0;
  height: 0;
}
.accordion__btn p {
  color: var(--brown);
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  text-transform: uppercase;
}
.accordion__btn svg {
  width: 15px;
  height: 15px;
  margin-top: 5px;
  margin-left: 15px;
  flex-shrink: 0;
}
.accordion-content {
  display: none;
}
.accordion-content.active {
  display: block;
}
.accordion-content ol,
.accordion-content ul {
  margin: 0;
  padding-left: 20px;
}
.accordion-content p,
.accordion-content li {
  color: var(--brown);
}
.accordion-content li {
  list-style-type: decimal;
}
.accordion-content li:not(:last-child) {
  margin-bottom: 12px;
}
.accordion-content .dotted {
  list-style-type: disc;
}
.accordion-content p:not(:last-child) {
  margin-bottom: 24px;
}

@media screen and (max-width: 575px) {
  .accordion--border::before {
    padding-top: 20px;
  }
  .accordion-item:not(:first-child) {
    margin-top: 20px;
  }
  .accordion-item::after {
    padding-bottom: 20px;
  }
  .accordion__btn.active {
    margin-bottom: 20px;
  }
  .accordion__btn p {
    line-height: 18px;
  }
  .accordion__btn svg {
    min-width: 11px;
    min-height: 11px;
    width: 11px;
    height: 11px;
    margin-top: 3px;
  }
  .accordion-content ol,
  .accordion-content ul {
    margin: 0;
    padding-left: 16px;
  }
  .accordion-content p,
  .accordion-content li {
    color: var(--brown);
    font-size: 12px;
    font-weight: 400;
    line-height: 18px;
  }
  .accordion-content li {
    list-style-type: decimal;
  }
  .accordion-content li:not(:last-child) {
    margin-bottom: 12px;
  }
  .accordion-content .dotted {
    list-style-type: disc;
  }
  .accordion-content p:not(:last-child) {
    margin-bottom: 24px;
  }
}
.modal {
  --overlay-color: rgba(0, 0, 0, 0.5);
  --crossHeight: 20px;
  --max-width: 878px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--overlay-color);
  z-index: 100;
  overflow-x: hidden;
  overflow-y: auto;
  display: none;
  animation: fadeOut 0.25s both ease;
}
.modal__overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--overlay-color);
}
.modal--open {
  animation: fadeIn 0.25s both ease;
  display: block;
}
.modal__content {
  border-radius: 20px;
  background: #faf9f8;
  overflow: hidden;
  width: 100%;
  position: relative;
  visibility: hidden;
  opacity: 0;
  transform: translateY(-15%);
  transition: 0.25s ease;
  cursor: default;
  max-width: var(--max-width);
  margin: 0 auto;
  margin-top: 50px;
}
.modal__content--open {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}
.modal__close {
  border: 0;
  padding: 0;
  background: 0;
  font-size: var(--crossHeight);
  font-weight: bold;
  width: var(--crossHeight);
  height: var(--crossHeight);
  line-height: var(--crossHeight);
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  right: 22px;
  top: 22px;
  cursor: pointer;
  transition: var(--transition);
}
.modal__body {
  display: flex;
  gap: 52px;
  padding: 47px 53px 48px 42px;
}
.modal__body img {
  max-width: 300px;
  max-height: 410px;
  border-radius: 10px;
  border: 1px solid #d6d1ca;
}
.modal__body-img {
  position: relative;
  align-self: flex-start;
  margin: 0 auto;
}
.modal__body-text {
  display: flex;
  flex-direction: column;
}
.modal__body-text p {
  color: var(--brown);
  margin-bottom: 24px;
}
.modal__body-text .cardAuthor {
  font-style: italic;
  color: var(--dark-gray);
  margin-bottom: 32px;
}
.modal__body-text .cardCreator {
  margin-bottom: 0;
}
.modal__body-text .creator {
  margin-bottom: 23px;
  position: relative;
  padding-top: 24px;
}
.modal__body-text .creator a {
  color: var(--brown);
}
.modal__body-text .creator::before {
  content: "";
  position: absolute;
  top: 0;
  display: block;
  width: 100%;
  height: 1px;
  background: #d6d1ca;
}
.modal__body-text .socials {
  display: flex;
  gap: 13px;
}

@media screen and (max-width: 991px) {
  .modal {
    --max-width: 650px;
  }
  .modal__body {
    gap: 32px;
  }
  .modal__body-img img {
    max-width: 260px;
  }
}
@media screen and (max-width: 767px) {
  .modal {
    --max-width: 450px;
  }
  .modal__body {
    flex-direction: column;
    align-items: center;
    gap: 22px;
  }
}
@media screen and (max-width: 575px) {
  .modal {
    --max-width: 343px;
  }
