  z-index: 21;
}
.header__socials-links a:hover svg rect,
.header__socials-links a:hover svg circle,
.header__socials-links a:hover svg path {
  opacity: 1;
  transition: var(--transition);
}

@media screen and (max-width: 991px) {
  .header--light .header__link {
    color: #faf9f8;
  }
  .header--light .header__link.active {
    border-bottom: 0;
    padding-bottom: 0;
  }
  .header--light .header__socials-links a {
    opacity: 1;
  }
  .header__menu {
    display: block;
  }
  .header__nav {
    visibility: hidden;
    opacity: 0;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 20;
    transition: 0.5s;
    background-color: var(--dark-gray);
    overflow: hidden;
  }
  .header__nav.active {
    visibility: visible;
    opacity: 1;
  }
  .header__nav.active + .header__socials-links a {
    opacity: 1;
    transition: var(--transition);
  }
  .header__nav.active + .header__socials-links a svg rect {
    stroke: var(--peach);
  }
