    stroke: var(--peach);
  }
  .header__nav.active + .header__socials-links a svg circle[fill] {
    fill: var(--peach);
  }
  .header__nav.active + .header__socials-links a:hover {
    opacity: 1;
  }
  .header__list {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 8px;
    margin-top: 67px;
    padding: 0 16px;
    padding-top: 80px;
    border-top: 1px solid rgba(239, 224, 208, 0.3);
  }
  .header__link {
    color: #faf9f8;
    opacity: 1;
    font-family: var(--second-font);
